import {CardType} from './CardType'


class GamePlayerDTO {
    public UserID: number;
    public IsOnline: boolean;
    public IsVIP: boolean;
    public Nick: string;
    public AvatarUri: string;
    public IsReady: boolean;
    public BirthDate: Date;
    public GenderID: number;
    public Position: number;
    public GameLeaveCount: number;
    public GamerRaiting: number;
    public Place: number;
    public AddedPoints: number;
    public AddedRating: number;
    public MusicChannelID: number;
}



export class GamePlayer extends GamePlayerDTO {
    Cards: CardType[]
    LevelName: string
    Want: number
    Took: number
    RelationStatusID: number
    IsBlocked: boolean
    IsFrendRequestSent: boolean
    MusicChannelName: string


    static CreateMock(userId, nick, avatar, isOnline = true): GamePlayer {
        var item = new GamePlayer();
        item.Nick = nick;
        item.UserID = userId;
        item.AvatarUri = avatar;
        item.Want = 2;
        item.Took =  Math.round(Math.random() * 3)
        item.IsOnline = isOnline
        item.LevelName = ""
        item.MusicChannelName = "HITS d daawd wadawdawa"
        return item;
    }
}