
export class ResultsModel {
    Sections: ResultSection[]
}

export class ResultSection {
    Players: ResultSectionItems[]
    IsActive: boolean
}

export class ResultSectionItems {
    Items: ResultItem[]
    Total: number
    IsBonus: boolean
    IsRemoved: boolean
    UserID: number
    Position: number
    BonusScore: number
}

export class ResultItem {
    Want: number
    Score: number
    IsBonus: boolean
    IsRemoved: boolean

    isGood() {
        return (this.Score >= 100 || (this.Want == 0 && this.Score == 50)) && !this.isPending()
    }

    isBad() {
        return !this.isGood() && !this.isPending()
    }

    isPending() {
        return (this.Want == undefined) || (this.Want == null) || (this.Score == undefined) || (this.Score == null)
    }


    static Create(want, score, isBonus = false, isRemoved = false) {
        var item = new ResultItem();
        item.Want = want;
        item.Score = score;
        item.IsBonus = isBonus;
        item.IsRemoved = isRemoved;

        return item;
    }
}