import {Injector} from 'angular2/core'
import {Cookie} from './Cookie';
import {ConfigProvider} from './Providers/ConfigProvider'
import {Http, HTTP_PROVIDERS, Headers, Response, Request} from 'angular2/http'

declare var window: any
declare var UAParser: any


export class JokExceptionHandler {
    savedErrors = []
    hashCodes = []

    private config: ConfigProvider
    private http: Http

    lastSendDate: number


    constructor() {
        window.Errors = this.savedErrors

        var injector = Injector.resolveAndCreate([
            HTTP_PROVIDERS,
            ConfigProvider
        ])

        this.config = injector.get(ConfigProvider)
        this.http = injector.get(Http)
    }


    call(exception: Exception, stackTrace?: any, reason?: string): void {

        var logEx = exception.originalException || exception

        try {

            var userAgent;
            var info = new UAParser().getResult()
            if (info) {
                userAgent = {
                    BrowserName: info.browser.name,
                    BrowserVersion: info.browser.version,
                    OSName: info.os.name,
                    OSVersion: info.os.version,
                    CPUArchitecture: info.cpu.architecture,
                    DeviceModel: info.device.model,
                    DeviceType: info.device.type,
                    DeviceVendor: info.device.vendor
                }
            }

            var logObj: any = {
                GameID: this.config.gameId,
                GameClientVersion: this.config.gameClientVersion,
                HTMLBody: document.body ? (document.body.outerHTML || document.body.innerHTML) : '',
                ErrorMessage: logEx.message,
                ErrorStack: logEx.stack,
                Reason: reason,
                UserAgent: userAgent,
                SID: localStorage['sid'] || Cookie.getCookie('sid'),
                ViewModel: JSON.stringify(window.VM),
                Language: this.config.activeLang
            }

            var hashCode = this.hashCode(logEx.message)
            if (~this.hashCodes.indexOf(hashCode)) return

            this.hashCodes.push(hashCode)
            this.savedErrors.push(logObj)

            if (this.lastSendDate && (Date.now() - this.lastSendDate) < 1000) return


            // save to server
            this.http.post(this.config.apiUrl + '/Game/LogError?sid=' + (localStorage['sid'] || Cookie.getCookie('sid')), JSON.stringify(logObj)).subscribe()
            this.lastSendDate = Date.now()
        }
        catch (err) {
            alert('Unhandled: ' + err)
        }
        finally {
            console.warn(logEx);
            console.error(exception, stackTrace, reason)
        }
    }


    hashCode(s: string): number {
        if (!s)
            return null

        var hash = 0, i, chr, len;
        if (s.length === 0) return hash;
        for (i = 0, len = s.length; i < len; i++) {
            chr = s.charCodeAt(i);
            hash = ((hash << 5) - hash) + chr;
            hash |= 0; // Convert to 32bit integer
        }
        return hash;
    };
}

interface Exception {
    message: string
    stack: string

    originalException?: Exception
}