import {Injectable, EventEmitter} from 'angular2/core'


@Injectable()
export class DownCardsProvider {

    animateEvent = new EventEmitter<number>()
    animateCompletedEvent = new EventEmitter()

    private animateCompletedCallback = null

    constructor() {
        this.animateCompletedEvent.subscribe(() => {
            this.animateCompletedCallback && this.animateCompletedCallback()
        })
    }

    animate(position: number, cb?: () => void) {
        this.animateEvent.emit(position)
        this.animateCompletedCallback = cb
    }

}