import {Injectable, NgZone} from 'angular2/core'

import {IOSPlatformProvider} from '../ios.provider'
import {AndroidPlatformProvider} from '../android.provider'
import {WindowsPhonePlatformProvider} from '../wp.provider'
import {FacebookPlatformProvider} from '../facebook.provider'
import {BrowserPlatformProvider} from '../browser.provider'


declare var window: any


export class Platform {

    public static Get(): Platforms {

        var result, platform = localStorage.getItem('platform') || window.platform

        switch (platform) {
            case 'ios':
                result = Platforms.IOS
                break

            case 'android':
                result = Platforms.Android
                break

            case 'wp':
                result = Platforms.WindowsPhone
                break

            case 'facebook':
                result = Platforms.Facebook
                break

            default:
                result = Platforms.Browser
                break
        }

        if (result != Platforms.Browser && result != Platforms.Facebook)
            localStorage.setItem('platform', platform)

        return result
    }

    public static GetPlatform(): any {

        switch (Platform.Get()) {
            case Platforms.IOS:
                return IOSPlatformProvider

            case Platforms.Android:
                return AndroidPlatformProvider

            case Platforms.WindowsPhone:
                return WindowsPhonePlatformProvider

            case Platforms.Facebook:
                return FacebookPlatformProvider

            default:
                return BrowserPlatformProvider
        }
    }

    public static IsMobile() {
        var platform = Platform.Get()

        return !!~[Platforms.IOS, Platforms.Android, Platforms.WindowsPhone].indexOf(platform)
    }
}



export enum Platforms {
    Browser = 1,
    IOS = 2,
    Android = 3,
    WindowsPhone = 4,
    Facebook = 5
}