    private CurrentTable(table: GameTable, channel) {

        this.tableId = table.ID
        this.VM.Status = table.Status
        this.VM.Dring = table.Xishti
        this.VM.TableChannel = table.Channel
        this.VM.TableType = table.Type
        this.VM.IsTableInfoReceived = true

        this.TableReset()

        switch (this.VM.Status) {

            case TableStatuses.New: {

                if (table.Channel) {
                    this.VM.InviteLink = location.origin + location.pathname + (location.pathname.lastIndexOf('/') == location.pathname.length - 1 ? '' : '/')
                    this.chat.addItem(ChatItemMode.Jok, this.CHAT_SENDER_JOK, this.VM.InviteLink, ChatItemTemplate.ServerPrivateTable, null, null, null)
                }
                else {
                    this.chat.addItem(ChatItemMode.Jok, this.CHAT_SENDER_JOK, this.VM.InviteLink, ChatItemTemplate.ServerPublicTable, null, null, null)
                }

                this.VM.Results.Sections.forEach(x => x.IsActive = false)
                this.VM.Results.Sections[0].IsActive = true

                break
            }


            case TableStatuses.Started:
            case TableStatuses.Stopped: {

                this.VM.NotificationType = null
                this.VM.Results.Sections.forEach(x => x.IsActive = false)
                this.VM.Results.Sections[0].IsActive = true

                this.TableReset()

                break
            }


            case TableStatuses.Finished: {

                this.VM.Results.Sections.forEach(x => x.IsActive = false)
                this.VM.Results.Sections[3].IsActive = true

                // [FINISH]

                var winners = []
                table.Players.forEach((p, i) => {

                    var item = {
                        UserID: p.UserID,
                        Nick: null,
                        IsOnline: p.IsOnline,
                        Place: p.Place,
                        Points: p.AddedPoints,
                        Rating: p.AddedRating,
                        Score: ''
                    }

                    this.usersProvider.get(p.UserID, u => item.Nick = u.Nick)

                    winners.push(item)
                })

                table.Players = table.Players.sort((a, b) => {
                    return a.Place - b.Place
                })


                this.VM.FinishInfo.Key = table.FinishResultKey
                this.VM.FinishInfo.Players = winners

                break
            }
        }

        this.CurrentTableUsers(table.Players)
    }

    private CurrentTableUsers(players: GamePlayer[]) {

        if (!this.lastPlayersCount || (this.lastPlayersCount < players.length)) {
            this.playAudio(PlayAudioModes.PlayerLogin)
        }
        this.lastPlayersCount = players.length

        this.VM.NotificationType = null


        //   
        if (players) {
            players.forEach(x => {
                if (!x) return

                x.LevelName = 'playerLevels.Refreshing'
                x.MusicChannelName = this.music.getChannelName(x.MusicChannelID)

                this.usersProvider.get(x.UserID, user => {
                    x.Nick = user.Nick
                    x.AvatarUri = user.AvatarUrl
                    x.LevelName = user.Game ? this.currentUser.getPlayerLevelName(user.Game.RatingStars) : ''
                    x.RelationStatusID = x.IsFrendRequestSent ? UserRelationStatuses.Pending : user.RelationStatusID2
                    x.IsBlocked = this.blockedUserIds.indexOf(user.UserID) > -1
                })
            })
        }


        // Players-  (+)
        if (players.length == 4) {
            this.VM.Players = [null, null, null, null]
            this.VM.ResultPlayers = [null, null, null, null]

            if (this.VM.Status == TableStatuses.New) {
                players.forEach((x, i) => this.VM.Players[i] = x)

                this.VM.ResultPlayers = this.VM.Players

                this.VM.NotificationType = NotificationType.GameStarting

                if (!this.startCountDownInterval) {
                    this.VM.GameStartSeconds = 10
                    clearInterval(this.startCountDownInterval)
                    this.startCountDownInterval = setInterval(() => {
                        if (--this.VM.GameStartSeconds == 1)
                            clearInterval(this.startCountDownInterval)
                    }, 1000)
                }
            }
            else {
                players.forEach(x => x.Position && (this.VM.Players[x.Position - 1] = x))

                this.VM.ResultPlayers = this.VM.Players.slice()

                this.VM.Results.Sections.forEach(x => x.Players.forEach((p, i) => {
                    p.UserID = this.VM.ResultPlayers[i].UserID
                    p.Position = this.VM.ResultPlayers[i].Position
                }))

                var currentPlayerPosition = players.filter(x => x.UserID == this.config.currentUserId).map(x => x.Position)[0]
                var rotateRate = 4 - (currentPlayerPosition - 1)

                for (let i = 0; i < rotateRate; i++) {
                    this.VM.Players.unshift(this.VM.Players.pop())
                }
            }
        }

        else {
            clearInterval(this.startCountDownInterval)
            this.startCountDownInterval = null

            if (this.VM.Status == TableStatuses.New) {
                if (this.VM.TableChannel) {
                    this.VM.InviteLink = location.origin + location.pathname + (location.pathname.lastIndexOf('/') == location.pathname.length - 1 ? '' : '/')
                    //this.VM.NotificationType = NotificationType.WaitingForFriend
                }
                else {
                    //this.VM.NotificationType = NotificationType.WaitingForOpponent
                }
            }
        }

        // Current Players- 
        this.VM.CurrentPlayer = players.filter(x => x.UserID == this.config.currentUserId)[0]




        switch (this.VM.Status) {

            case TableStatuses.New: {

                this.VM.PositionSelectionPlayers = [null, null, null, null]

                if (players) {
                    players.forEach(x => x.Position && (this.VM.PositionSelectionPlayers[x.Position - 1] = x))
                    this.VM.ResultPlayers = this.VM.Players = players
                }

                break
            }


            case TableStatuses.Started:
            case TableStatuses.Stopped: {

                break
            }


            case TableStatuses.Finished: {

                break
            }
        }
    }

