import {Component, provide} from 'angular2/core'

import {CommunicationClientProvider, MockClientProvider} from 'Jok/Communication'

import {UIGame} from '../game'
import {ViewModelProvider, MockViewModelProvider} from '../viewmodel.provider'


@Component({
    selector: 'mock',
    styles: [`game {  position: absolute;  left: 0;  right: 0;  top: 0;  bottom: 0; }`],
    template:'<game></game>',
    directives: [UIGame],
    providers: [
        provide(ViewModelProvider, { useClass: MockViewModelProvider }),
        provide(CommunicationClientProvider, { useClass: MockClientProvider }),
    ]
})
export class UIMock {
}

