
export class ChatItem {
    MessageIds: string[]
    Messages: string[]
    Nick: string
    AvatarUrl: string
    Mode: ChatItemMode
    Template: ChatItemTemplate
    UserId: number
    Timestamp: Date
    ShowHeader: boolean
}

export enum ChatItemMode {
    Jok,
    CurrentPlayer,
    player2,
    player3,
    player4
}

export enum ChatItemTemplate {
    Default,
    ServerPublicTable,
    ServerPrivateTable,
    ServerInviteFriend,
    GameStarting
}
