import {Injectable} from 'angular2/core'
import {Http} from 'angular2/http'
import {Cookie} from '../Cookie'

import {Platform, Platforms} from 'Jok/Platforms'

declare var $, window: any


@Injectable()
export class ConfigProvider {

    gameId: number = 12
    gameClientVersion: string = 'ng2-' + window.ClientVersion
    channel: string = 'test'
    mode: number = 1
    levelControl: number
    starsControl: number
    gameServerUrl: string = 'https://jok.io:8082'
    portalServerUrl: string = 'https://jok-realtime-server.herokuapp.com:443'
    exitUrl: string = 'http://jok.io/game/lobby/matrix'
    apiUrl: string = 'https://api.jok.io'
    loginUrl: string = 'http://jok.io/joinus?returnUrl='
    enableMessageLogging: boolean = true;
    protocols: string[] = ['polling', 'websocket']
    sid: string = localStorage['sid'] || Cookie.getCookie('sid')
    playUrl: string = location.origin + location.pathname + (location.pathname.lastIndexOf('/') == location.pathname.length - 1 ? '' : '/')
    playPublicUrl2: string = location.origin + '/quickplay'
    playPublicUrl: string = location.origin + '/play'
    playPublic9Url: string = location.origin + '/play/mode/1'
    currentUserId: number
    isAudioEffectsEnabled: boolean
    activeLang: string
    isCompact: boolean
    platform: Platforms = Platform.Get()

    langMap = {
        1: 'ka',
        2: 'en',
        3: 'ru'
    }




    constructor(private http: Http) {

        window.Config = this;

        var audiosEnabled = localStorage['audio-enabled']
        this.activeLang = localStorage['lang']

        this.isAudioEffectsEnabled = (audiosEnabled == undefined) ? true : (audiosEnabled === 'true')

        this.refreshCompactMode()

        $(window).resize(() => this.refreshCompactMode())

        this.refreshConfig()
        setInterval(() => this.refreshConfig(), 1000 * 60 * 3)
    }


    public isMobile(): boolean {
        return !!~[Platforms.IOS, Platforms.Android, Platforms.WindowsPhone].indexOf(this.platform)
    }

    public isSocialPlatform(): boolean {
        return !!~[Platforms.Facebook].indexOf(this.platform)
    }

    public changeAudioOption(isOn: boolean) {
        this.isAudioEffectsEnabled = isOn
        localStorage['audio-enabled'] = isOn
    }

    public save() {
        //Cookie.setCookie('sid', this.sid, 1024, '/', this.getHostName())
        localStorage['sid'] = this.sid
        localStorage['lang'] = this.activeLang
    }

    public refreshCompactMode() {
        this.isCompact = this.isMobile() || ($(window).height() < 600)
    }

    public getHostName() {
        try {
            var host = location.hostname

            var parts = host.split('.');
            if (parts.length <= 1)
                return host

            return ['', parts[parts.length - 2], parts[parts.length - 1]].join('.')  //sample: '.jok.ge'

        }
        catch (err) {
            return location.hostname
        }
    }

    public getLanguageId(lang: string) {
        if (this.langMap[2] == lang)
            return 2
        if (this.langMap[3] == lang)
            return 3

        return 1
    }

    
    private refreshConfig() {
        this.http.get(this.apiUrl + '/Joker/Config').subscribe(res => {
            var serverConfig = res.json();
            if (!serverConfig || !serverConfig.IsSuccess) return

            this.gameServerUrl = serverConfig.GameServerUrl
            this.portalServerUrl = serverConfig.PortalServerUrl
        })
    }
}
