import {Injectable, NgZone} from 'angular2/core'
import {Http} from 'angular2/http'
import {Router} from 'angular2/router'
import {PlatformProvider} from './Common/platform.provider'

import {ConfigProvider} from '../../Common/Providers/All'


@Injectable()
export class FacebookPlatformProvider extends PlatformProvider {

    cache = {}

    APPID = '1145415865488073'


    constructor(private router: Router, private http: Http, private config: ConfigProvider) {
        super()
    }


    playAudio(name: string, ext: string = 'mp3') {

        var audio = this.cache[name + ext] || new Audio('/audios/' + name + '.' + ext)
        this.cache[name + ext] = audio

        try {
            audio.pause()
            audio.currentTime = 0
            audio.play()
        }
        catch (err) { }
    }

    initStore(hideLoader?: boolean) {

        var appid = this.APPID;
        var initFunc = function () {
            FB.init({
                appId: appid,
                xfbml: true,
                version: 'v2.6'
            });
        }

        if (typeof FB == "undefined") {
            window.fbAsyncInit = initFunc
        }
        else {
            initFunc()
        }
    }

    buyItem(productId: string) { }

    initGameCenter() { }

    submitScore(score: number) { }

    checkInternetConnection() { }

    showLoading(title?: string) { }

    hideLoading() { }

    saveToken(token: string) { }

    deviceRegistration() { }

    login(returnUrl?: string) {
        FB.login((response) => {

            if (response.status === 'connected') {

                var token = response.authResponse.accessToken
                var languageId = this.config.getLanguageId(this.config.activeLang)

                this.http.post(this.config.apiUrl + '/User/FacebookLogin?fbToken=' + token + '&languageId=' + languageId, null).subscribe(res => {
                    var result = res.json();
                    if (!result || !result.IsSuccess) {
                        alert('Facebook login failed')
                        console.warn(result)
                        return
                    }

                    this.emit(PlatformProvider.REQUEST_SAVE_TOKEN_EVENT, result.Token)

                    setTimeout(() => {
                        if (returnUrl)
                            document.location.assign(returnUrl)
                    }, 500)
                })

            } else if (response.status === 'not_authorized') {

            } else {

            }
        }, { scope: "email,public_profile,user_friends" });
    }

    viewProfile(userid: number) {
        this.emit(PlatformProvider.VIEW_PROFILE_EVENT, userid)
    }
}
