

export class JF {

    public static SetInRange(i: number, minRange: number, maxRange: number): number {
        if (i < minRange)
            i = minRange;

        if (i > maxRange)
            i = maxRange;

        return i;
    }

    public static Next(i: number, min: number, max: number): number {
        if ((i >= min) && (i < max))
            return ++i;
        return min;
    }

    public static Previous(i: number, min: number, max: number): number {
        if ((i > min) && (i <= max))
            return --i;
        return max;
    }
}