import {Injectable, NgZone} from 'angular2/core'

declare var window: any


@Injectable()
export abstract class PlatformProvider extends EventEmitter {

    static REQUEST_SAVE_TOKEN_EVENT = "REQUEST_SAVE_TOKEN_EVENT"
    static VIEW_PROFILE_EVENT = "VIEW_PROFILE_EVENT"


    isStoreReady: boolean = null

    abstract playAudio(name: string, ext?: string);

    abstract initStore(hideLoader?: boolean);

    abstract buyItem(productId: string);

    abstract initGameCenter();

    abstract submitScore(score: number);

    abstract checkInternetConnection();

    abstract showLoading(title?: string);

    abstract hideLoading();

    abstract saveToken(token: string);

    abstract deviceRegistration();

    abstract login(returnUrl?: string);

    abstract viewProfile(userid: number);
}
