import 'rxjs/Rx'
import {Injectable, NgZone} from 'angular2/core'
import {Http} from 'angular2/http'
import {CurrentUserProvider} from './CurrentUserProvider'
import {ConfigProvider} from './ConfigProvider'

declare var eio, Buffer, window: any


@Injectable()
export class PortalProvider extends EventEmitter {

    public OnlineUsersCount: number

    public MusicChannelTracks: { [index: string]: TrackInfo } = {}

    public Rooms: Room[] = []

    public RoomConfirmations: RoomConfirmationPlayer[] = null
    public IsConfirmationButtonsAllowed = true


    private socket: any
    private pendingReceivingRoomEvents = []


    constructor(private currentUser: CurrentUserProvider, private config: ConfigProvider, private zone: NgZone) {
        super()

        if (currentUser.UserId)
            this.start()
        else
            currentUser.onReady(this.onReady.bind(this))

        this.currentUser.on(CurrentUserProvider.AuthentificationChangeEvent, this.restart.bind(this))
    }


    // Public API
    onReady() {
        this.start()

        while (this.pendingReceivingRoomEvents.length) {
            this.startReceivingRoomEvents(this.pendingReceivingRoomEvents.pop())
        }

        this.currentUser.onReady(() => { })
    }

    restart() {
        //this.stop()
        //this.start()
    }

    start() {

        var url = this.config.portalServerUrl + '?gameid=' + 6 /*Portal Service*/ + '&userinfo=' + this.btoa(this.currentUser.UserId)
        this.socket = eio.Socket(url)

        this.socket.send = function (command, data, fn) {
            this.sendPacket('message', JSON.stringify({ c: command, d: data }), fn);
            return this;
        }

        this.socket.on('message', (msg) => {

            try {
                msg = JSON.parse(msg);
            }
            catch (err) {
                return;
            }

            if (!msg || !msg.c) return;

            this.zone.run(() => this.emit(msg.c, msg.d))
        })

        this.on(ClientCommands.OnlineUsersCount, this.onOnlineUsersCount.bind(this))
        this.on(ClientCommands.MusicChannelUpdate, this.onMusicChannelUpdate.bind(this))

        this.on(ClientCommands.RoomCreate, this.onRoomCreate.bind(this))
        this.on(ClientCommands.RoomInfo, this.onRoomInfo.bind(this))
        this.on(ClientCommands.RoomRemove, this.onRoomRemove.bind(this))
        this.on(ClientCommands.RoomUserJoinFailed, this.onRoomUserJoinFailed.bind(this))

        this.on(ClientCommands.RoomConfirmationStart, this.onRoomConfirmationStart.bind(this))
        this.on(ClientCommands.RoomConfirmationUpdate, this.onRoomConfirmationUpdate.bind(this))
        this.on(ClientCommands.RoomConfirmationFinish, this.onRoomConfirmationFinish.bind(this))

    }

    stop() {
        this.removeEvent()

        if (this.socket)
            this.socket.close()
        else
            this.socket = null
    }

    send(command, data, fn?) {
        this.socket.sendPacket('message', JSON.stringify({ c: command, d: data }), fn);
    }

    startReceivingRoomEvents(gameId: number) {
        window.Rooms = this.Rooms = []
        //this.RoomConfirmations = null

        if (!this.socket) {
            this.pendingReceivingRoomEvents.push(gameId)
            return
        }

        this.send(ServerCommands.RoomEventsSubscribe, { GameID: gameId })
    }

    stopReceivingRoomEvents(gameId: number) {
        this.send(ServerCommands.RoomEventsUnsubscribe, { GameID: gameId })
    }

    createRoom(levelControl, gameoption, gameid) {
        this.send(ServerCommands.RoomCreate, {
            GameID: gameid,
            LevelControl: levelControl.toString(),
            GameOption: gameoption
        })
    }

    joinRoom(roomid, gameid) {
        this.send(ServerCommands.RoomJoin, {
            GameID: gameid,
            RoomID: roomid
        })
    }

    leaveRoom(roomid, gameid) {
        this.send(ServerCommands.RoomLeave, {
            GameID: gameid,
            RoomID: roomid
        })
    }

    kickPlayer(roomid, userid, gameid) {
        this.send(ServerCommands.RoomUserKick, {
            GameID: gameid,
            RoomID: roomid,
            KickUserID: userid
        })
    }

    confirmationAnswer(roomid, isReady: boolean, gameid) {
        this.IsConfirmationButtonsAllowed = false

        this.send(ServerCommands.RoomConfirmationAnswer, {
            GameID: gameid,
            RoomID: roomid,
            Answer: isReady ? 1 : 2
        })
    }

    confirmationHide() {
        this.RoomConfirmations = null
    }


    // Event Listeners
    private onOnlineUsersCount(count) {
        count = parseInt(count)
        if (!count) return

        this.OnlineUsersCount = count
    }

    private onMusicChannelUpdate(data) {
        var channelid = data.MusicChannelID;
        var trackInfo = data.TrackInfo;
        var trackid = data.TrackID;

        if (!channelid || !trackInfo || !trackid) {
            delete this.MusicChannelTracks[channelid]
            return;
        }

        this.MusicChannelTracks[channelid] = {
            Id: trackid,
            Description: trackInfo
        }
    }

    private onRoomCreate(data) {

        var gameid = data.GameID;
        var roomid = data.RoomID;
        var levelControl = data.LevelControl;
        var gameOption = data.GameOption;
        var kickedUsers = data.KickedUsers || [];

        this.Rooms.push({
            Id: roomid,
            GameId: gameid,
            LevelControl: levelControl,
            GameOption: gameOption,
            KickedUsers: kickedUsers,
            IsHigherLevel: false,
            Players: []
        })
    }

    private onRoomInfo(data) {

        var roomid = data.RoomID;
        var users: any[] = data.Users;
        var kickedUsers: number[] = data.KickedUsers;
        var gameOption = data.GameOption;
        var levelControl = data.LevelControl;

        var room = this.Rooms.filter(x => x.Id == roomid)[0]
        if (!room) return

        room.KickedUsers = kickedUsers
        room.GameOption = gameOption
        room.LevelControl = levelControl
        room.Players = users.map(x => <RoomPlayer>{
            UserId: x.UserID,
            Nick: x.Nick,
            LevelId: x.LevelId
        })
    }

    private onRoomRemove(data) {
        var roomid = data.RoomID;

        var room = this.Rooms.filter(x => x.Id == roomid)[0]
        if (!room) return

        var index = this.Rooms.indexOf(room)
        if (!~index) return

        this.Rooms.splice(index, 1)
    }

    private onRoomUserJoinFailed() {
        // TODO: in view
    }

    private onRoomConfirmationStart(data) {
        var users: any[] = data.Users
        if (users.length != 4) return;

        this.IsConfirmationButtonsAllowed = true

        this.RoomConfirmations = users.map(x => <RoomConfirmationPlayer>{
            Nick: x.Nick,
            Answer: x.Answer
        })
    }

    private onRoomConfirmationUpdate(data) {
        var users: any[] = data.Users
        if (users.length != 4) return;

        this.RoomConfirmations = users.map(x => <RoomConfirmationPlayer>{
            Nick: x.Nick,
            Answer: x.Answer
        })
    }

    private onRoomConfirmationFinish(data) {
        var roomid = data.RoomID;
        var gameOption = data.GameOption;
        var isSuccess = data.IsGameStarting;

        //this.RoomConfirmations = null
    }


    // Helpers
    private btoa(str) {

        if (window.btoa)
            return window.btoa(str)

        return ''
    }
}


enum ServerCommands {
    GameOfferSend = 1,
    GameOfferAccept = 2,
    GameOfferDecline = 3,
    GameOfferCancel = 4,
    ChatRequest = 5,
    ChatMessage = 6,
    RoomEventsSubscribe = 7,
    RoomEventsUnsubscribe = 8,
    RoomCreate = 9,
    RoomJoin = 10,
    RoomLeave = 11,
    RoomUserKick = 12,
    RoomConfirmationStart = 13,
    RoomConfirmationAnswer = 14
}

export enum ClientCommands {
    MusicChannelUpdate = 1,
    FriendListClear = 2,
    FriendCameOnline = 3,
    FriendGoneOffline = 4,
    GameOffer = 5,
    GameOfferAccepted = 6,
    GameOfferDeclined = 7,
    GameOfferCanceled = 8,
    ChatStart = 9,
    ChatReceiveMessage = 10,
    RoomCreate = 11,
    RoomInfo = 12,
    RoomRemove = 13,
    RoomUserJoinFailed = 14,
    RoomConfirmationStart = 15,
    RoomConfirmationUpdate = 16,
    RoomConfirmationFinish = 17,
    OnlineUsersCount = 18
}

export interface TrackInfo {
    Id: number
    Description: string
}

export interface Room {
    Id: string
    LevelControl: number
    GameOption: number
    KickedUsers: number[]
    IsHigherLevel: boolean
    GameId: number

    Players: RoomPlayer[]
}

export interface RoomPlayer {
    UserId: number
    Nick: string
    LevelId: number
}

export interface RoomConfirmationPlayer {
    Nick: string
    Answer: RoomConfirmationPlayerAnswer
}

export enum RoomConfirmationPlayerAnswer {
    Waiting = 0,
    Ready = 1,
    NotReady = 2,
    Timeout = 3
}
