import {Directive, ElementRef} from 'angular2/core'

declare var $: any
declare var Clipboard: any


@Directive({
    selector: '[clipboard]'
})
export class ClipboardDirective {
    constructor(el: ElementRef) {

        var $el = $(el.nativeElement)

        var clipboard = new Clipboard(el.nativeElement)

        $el.attr('title', 'Copy to clipboard')
        $el.tooltip()

        clipboard.on('success', function (e) {
            $el.tooltip('destroy')
            $el.attr('title', 'Copied!')
            $el.tooltip('show')

            e.clearSelection()

            var timeout = $el.data('timeout')

            clearTimeout(timeout)
            timeout = setTimeout(() => $el.tooltip('destroy'), 3000)
            $el.data('timeout', timeout)
        })

        clipboard.on('error', function (e) {
            $el.tooltip('destroy')
            $el.attr('title', 'Press Ctrl+C to copy')
            $el.tooltip('show')

            var timeout = $el.data('timeout')

            clearTimeout(timeout)
            timeout = setTimeout(() => $el.tooltip('destroy'), 7000)
            $el.data('timeout', timeout)
        })

    }
}