import {Injectable} from 'angular2/core'

import {CardType, CardColor, CardLevel, ColorSelectionMode} from 'Jok/Cards'
import {ChatItem} from 'Jok/Chat'

import {GamePlayer, DeclarationModel, ResultsModel, ResultSectionItems, ResultItem, FinishInfoModel, NotificationType, TableStatuses} from '../Models/All'


declare var window: {
    VM: any
}

@Injectable()
export class ViewModelProvider {

    VM: ViewModel

    constructor() {
        this.VM = this.getEmpty();

        window.VM = this.VM
    }



    private getEmpty(): ViewModel {
        var result = new ViewModel()
        result.Players = []
        result.ResultPlayers = []
        result.PositionSelectionPlayers = []
        result.CurrentPlayerCards = []
        result.CurrentPlayer = new GamePlayer()
        result.DownCards = [null, null, null, null]
        result.Results = ViewModelProvider.EmptyResults()
        result.ChatItems = []
        result.DominatedColor = null
        return result
    }

    public static EmptyResults(existingPlayers?: ResultSectionItems[]): ResultsModel {
        function mockResultSectionPlayer() {
            return {
                Items: [
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                    ResultItem.Create(null, null),
                ],
                Total: null,
                IsBonus: false,
                IsRemoved: false,
                UserID: 1,
                Position: 1,
                BonusScore: 0
            }

        }

        function mockResultSection() {
            return {
                Players: [
                    mockResultSectionPlayer(),
                    mockResultSectionPlayer(),
                    mockResultSectionPlayer(),
                    mockResultSectionPlayer(),
                ],
                IsActive: false
            }
        }

        var mockResults = {
            Sections: [
                mockResultSection(),
                mockResultSection(),
                mockResultSection(),
                mockResultSection()
            ]
        }

        if (existingPlayers && existingPlayers.length == 4) {
            mockResults.Sections.forEach(y => y.Players.forEach((p, i) => {
                p.UserID = existingPlayers[i].UserID
                p.Position = existingPlayers[i].Position
            }))
        }

        return mockResults;
    }

    public static MockResults(existingPlayers?: ResultSectionItems[]): ResultsModel {
        function mockResultSectionPlayer() {
            return {
                Items: [
                    ResultItem.Create(2, 200, false, false),
                    ResultItem.Create(3, -100, false, false),
                    ResultItem.Create(1, 100, true, false),
                    ResultItem.Create(1, 100, false, true),
                    ResultItem.Create(2, 20, false, false),
                    ResultItem.Create(2, 30, false, false),
                    ResultItem.Create(2, 40, false, false),
                    ResultItem.Create(2, 10, false, false),
                ],
                Total: 0,
                IsBonus: true,
                IsRemoved: true,
                UserID: 1,
                Position: 1,
                BonusScore: 0
            }

        }

        function mockResultSection() {
            return {
                Players: [
                    mockResultSectionPlayer(),
                    mockResultSectionPlayer(),
                    mockResultSectionPlayer(),
                    mockResultSectionPlayer(),
                ],
                IsActive: true
            }
        }

        var mockResults = {
            Sections: [
                mockResultSection(),
                mockResultSection(),
                mockResultSection(),
                mockResultSection()
            ]
        }

        if (existingPlayers && existingPlayers.length == 4) {
            mockResults.Sections.forEach(y => y.Players.forEach((p, i) => {
                p.UserID = existingPlayers[i].UserID
                p.Position = existingPlayers[i].Position
            }))
        }

        return mockResults;
    }
}

