import {Injectable, NgZone} from 'angular2/core'

@Injectable()
export abstract class CommunicationClientProvider extends EventEmitter {

    private messagesLog = []
    private enableMessageLogging = false
    protected options: CommunicationOptions


    public abstract start()
    public abstract stop()
    protected abstract sendInternal(parameters: any[])

        
    constructor(private zone: NgZone) {
        super()
    }


    public setOptions(options: CommunicationOptions) {
        this.options = options
        this.enableMessageLogging = options.enableMessageLogging || false
    }

    public send(event: string, ...args: any[]) {
        try {
            var parameters: any[] = [event];
            parameters.push.apply(parameters, args);

            this.sendInternal(parameters)
        }
        catch (err) {
            console.error('[Send Failed]', event, args, err);
        }
    }


    protected messageReceived(data: any[]) {

        this.zone.run(() => {
            this.emit('MessageProceed', data)

            if (this.enableMessageLogging) {
                this.messagesLog.push(data)
            }

            this.emit.apply(this, data)

            this.emit('MessageProceedCompleted', data)
        })
    }
}


export interface CommunicationOptions {
    token: string
    channel: string
    gameMode: number
    levelControl: number
    starsControl: number
    clientAppInfo?: string
    url: string
    transports: string[]
    hubName?: string
    enableMessageLogging?: boolean
}