import {Directive, ElementRef, EventEmitter, Input, Output} from 'angular2/core'

declare var $: any

@Directive({
    selector: '[drag]'
})
export class DragDirective {

    @Input('dragEnabled')
    isEnabled: boolean

    @Output()
    dragclick = new EventEmitter();


    constructor(private el: ElementRef) {

        var $el = $(el.nativeElement)

        el.nativeElement.addEventListener('touchstart', e => this.onTouchStart(e, $el))
        el.nativeElement.addEventListener('touchmove', e => this.onTouchMove(e, $el))
        el.nativeElement.addEventListener('touchend', e => this.onTouchEnd(e, $el))
    }




    originalPageY
    moveLog: number[]
    lastLogDate
    lastLogDate2

    onTouchStart(e: TouchEvent, $el) {
        if (!this.isEnabled) return

        this.originalPageY = e.touches[0].pageY
        this.moveLog = []
        this.lastLogDate = Date.now()
        this.lastLogDate2 = Date.now()
    }

    onTouchEnd(e: TouchEvent, $el) {
        if (!this.isEnabled) return

        var delta = 0
        $el.css({
            transform: 'translateY(' + delta + 'px)',
            MozTransform: 'translateY(' + delta + 'px)',
            WebkitTransform: 'translateY(' + delta + 'px)',
            msTransform: 'translateY(' + delta + 'px)'
        })

        if (!this.moveLog || !this.moveLog.length) return
        this.moveLog.reverse()

        var isMoveForward = true
        var sum = this.moveLog.reduce((x, y, a) => {
            isMoveForward = isMoveForward && ((x - y) > 0)
            return y
        })

        this.moveLog = null

        if (isMoveForward) {
            this.dragclick.emit(null)
        }
    }

    onTouchMove(e: TouchEvent, $el) {
        if (!this.isEnabled) return

        e.preventDefault()

        var limit = 200

        var delta = e.changedTouches[0].pageY - this.originalPageY
        if (delta > 0)
            delta = 0

        if (delta < -limit)
            delta = -limit

        delta = Math.round(delta)


        var d = Date.now()
        if (d - this.lastLogDate > 100) {

            this.lastLogDate = d
            if (!this.moveLog)
                this.moveLog = []

            this.moveLog.push(Math.abs(delta))

            while (this.moveLog.length > 4)
                this.moveLog.shift()
        }

        var d = Date.now()
        if (d - this.lastLogDate2 > 40) {
            this.lastLogDate2 = d

            $el.css({
                transform: 'translateY(' + delta + 'px)',
                MozTransform: 'translateY(' + delta + 'px)',
                WebkitTransform: 'translateY(' + delta + 'px)',
                msTransform: 'translateY(' + delta + 'px)'
            })
        }

    }
}