
import {JokerTableResultType} from './JokerTableResultType'


export class TableResults2 {

    public SectionResult: number[] = [0, 0, 0, 0];
    public Total: number = 0;
    public BigestResult: number = 0;
    public BigestLineNo: number = 0;
    public SmallestResult: number = 0;
    public SmallestLineNo: number = 0;
}

export class TableminiResult2 {

    public Need: number;
    public Have: number;


    public Reset() {
        this.Have = 0;
        this.Need = 0;
    }

    public SetNeed(need: number) {
        this.Need = need;
    }

    public SetHave(have: number) {
        this.Have = have;
    }
}

export class TableJokerTableResults {

    constructor(
        public ResultType: JokerTableResultType,
        public SectionNo: number,
        public LineNo: number,
        public PlayerScore: number,
        public Position: number,
        public PlayerWant?: number,
        public PlayerTook?: number
        ) { }
}