import {Http} from 'angular2/http'

export class ListReader {
    public items: any[] = []
    public inProgress: boolean
    private lastGetItemsCount: number

    constructor(
        private url: string,
        private token: string,
        private batchCount: number = 10,
        private http: Http,
        loadFirstBatch = false
    ) {
        if (loadFirstBatch)
            this.load()
    }

    load(skip?: number) {
        if (!skip)
            this.items = []

        this.inProgress = true

        this.http.get(this.url + '?skip=' + skip + '&take=' + this.batchCount + '&sid=' + this.token).subscribe(x => {
            this.inProgress = false
            var res = x.json()

            if (!res || !res.IsSuccess || !res.Data) return

            this.lastGetItemsCount = res.Data.length
            this.items = this.items.concat(res.Data)
        })
    }

    isLoaded() {
        return !this.inProgress && this.items.length > 0
    }
}