import {Component, Input, Output, ElementRef, OnInit, EventEmitter} from 'angular2/core'
import {ROUTER_DIRECTIVES} from 'angular2/router'


@Component({
    host: {
        '(document:click)': 'onGlobalClick($event)',
    },
    selector: 'account',
    styles: [`.account header {  background: #34495E;  overflow: hidden;  cursor: pointer;  border-color: #34495E;  padding-left: 5px;  font-size: 13px;  padding-right: 45px;  min-width: 140px;  text-align: left; }  .account header:hover, .account header:active, .account header:focus {    text-decoration: none; }  .account header:hover {    border-color: #34495E; }  .account header div.avatar {    float: left;    width: 37px;    height: 40px; }    .account header div.avatar img {      height: 40px;      margin-right: 7px;      opacity: 0;      -moz-transition: all linear .1s;      -o-transition: all linear .1s;      -webkit-transition: all linear .1s;      transition: all linear .1s; }      .account header div.avatar img.transparent {        opacity: 0; }      .account header div.avatar img.loaded {        opacity: 1; }  .account header div.nick {    position: relative;    color: white;    margin-top: 4px; }  .account header div.info {    position: relative;    color: silver; }.account section {  background: rgba(0, 0, 0, 0.75);  padding: 15px;  position: absolute;  left: 0;  margin-top: 10px;  border-radius: 4px;  border: 1px solid #16a085;  min-width: 150px; }  .account section .btn-group {    margin-top: 5px;    margin-bottom: 10px; }  .account section button {    text-align: center; }  .account section:after, .account section:before {    bottom: 100%;    left: 24px;    border: solid transparent;    content: " ";    height: 0;    width: 0;    position: absolute;    pointer-events: none; }  .account section:after {    border-color: rgba(24, 134, 112, 0);    border-bottom-color: rgba(0, 0, 0, 0.65);    border-width: 7px;    margin-left: -7px; }  .account section:before {    border-color: rgba(22, 160, 133, 0);    border-bottom-color: #16a085;    border-width: 8px;    margin-left: -8px; }`],
    template:'<div class="account"><header (click)="onHeaderClick($event)" class="btn btn-default" (blur)="showSubmenu = false"><div class="avatar"><img [src]="avatarUrl" onload="javascript:$(this).addClass(\'loaded\')" [class.transparent]="!avatarUrl"></div><div class="nick"><span>{{nick}}</span></div><div class="info">{{info}}</div></header><section *ngIf="showSubmenu && (mode == 1)" class="menu"><div class="btn-group btn-group-justified" role="group"><a role="button" class="btn btn-primary btn-block" (click)="viewProfile()">{{\'account.ViewProfile\' | translate}}</a></div><div role="group"><button type="button" class="btn btn-danger btn-block" (click)="exit()">{{\'account.Logout\' | translate}}</button></div><div *ngIf="isAdmin" role="group"><hr><a type="button" class="btn btn-block btn-warning" href="http://dev.jok.ge">Dev Portal</a> <button type="button" class="btn btn-block btn-warning" [routerLink]="[\'/Play\', \'Mock\']">Play Mock</button></div></section><section *ngIf="showSubmenu && (mode == 2)" class="menu"><div *ngIf="false" class="btn-group btn-group-justified" role="group"><a class="btn btn-primary btn-block">{{\'account.Feedback\' | translate}}</a></div><div *ngIf="!isMobile" class="btn-group btn-group-justified" role="group"><a class="btn btn-primary btn-block" (click)="clearChat()">{{\'account.ClearChat\' | translate}}</a></div><div class="btn-group btn-group-justified" role="group"><a role="button" class="btn" [ngClass]="audioOffClass" (click)="changeAudioOption(false)"><i class="fa fa-volume-off"></i></a> <a role="button" class="btn" [ngClass]="audioOnClass" (click)="changeAudioOption(true)"><i class="fa fa-volume-up"></i></a></div><div class role="group"><button type="button" class="btn btn-danger btn-block" (click)="exit()">{{\'account.LeaveGame\' | translate}}</button></div></section></div>',
    directives: [ROUTER_DIRECTIVES]
})
export class UIAccount implements OnInit {

    @Input()
    isAdmin: boolean

    @Input()
    nick: string

    @Input()
    avatarUrl: string

    @Input()
    info: string

    @Input()
    isAudioEffectsEnabled: boolean

    @Input()
    isMobile: boolean

    @Input()
    mode: ViewMode

    @Input()
    callbacks: AccountCallbacks


    showSubmenu: boolean


    audioOnClass = this.getAudioOnClassMap()
    audioOffClass = this.getAudioOffClassMap()


    constructor(private accountElement: ElementRef) {
    }

    ngOnInit() {
        this.changeAudioOption(this.isAudioEffectsEnabled)
    }

    onGlobalClick() {
        this.showSubmenu = false
    }

    onHeaderClick(e: MouseEvent) {
        this.showSubmenu = !this.showSubmenu

        e.stopPropagation()
    }

    getAudioOnClassMap() {
        return {
            'btn-primary': !!this.isAudioEffectsEnabled,
            'btn-default': !this.isAudioEffectsEnabled,
            'active': !!this.isAudioEffectsEnabled
        }
    }

    getAudioOffClassMap() {
        return {
            'btn-warning': !this.isAudioEffectsEnabled,
            'btn-default': !!this.isAudioEffectsEnabled,
            'active': !this.isAudioEffectsEnabled
        }
    }

    changeAudioOption(isOn: boolean) {
        this.callbacks && this.callbacks.onChangeAudioOption && this.callbacks.onChangeAudioOption(isOn)
        this.isAudioEffectsEnabled = isOn

        this.audioOnClass = this.getAudioOnClassMap()
        this.audioOffClass = this.getAudioOffClassMap()

        this.showSubmenu = false
    }

    viewProfile() {
        this.callbacks && this.callbacks.onViewProfile && this.callbacks.onViewProfile()
        this.showSubmenu = false
    }

    exit() {
        this.callbacks && this.callbacks.onExit && this.callbacks.onExit()
        this.showSubmenu = false
    }

    clearChat() {
        this.callbacks && this.callbacks.onClearChat && this.callbacks.onClearChat()
        this.showSubmenu = false
    }
}

export interface AccountCallbacks {
    onExit: () => void

    onChangeAudioOption?: (isOn: boolean) => void
    onViewProfile?: () => void
    onClearChat?: () => void
}


enum ViewMode {
    Portal = 1,
    Game = 2
}