import {Injectable} from 'angular2/core'

import {CardType, CardColor, CardLevel, ColorSelectionMode} from 'Jok/Cards'
import {ChatItem} from 'Jok/Chat'

import {GamePlayer, NotificationType, TableStatuses} from './game.model'
import {FinishInfoModel, FinishInfoPlayer} from './finishinfo.model'

declare var window: {
    VM: any
}


@Injectable()
export class ViewModelProvider {

    VM: ViewModel

    constructor() {
        this.VM = this.getEmpty();

        window.VM = this.VM
    }


    private getEmpty(): ViewModel {
        var result = new ViewModel()
        result.CurrentPlayer = new GamePlayer()
        result.OpponentPlayer = new GamePlayer()
        result.CurrentPlayerCards = []
        result.CurrentPlayerOpenCards = []
        result.OpponentPlayerOpenCards = []
        result.DownCards = [null, null, null, null]
        result.DominatedColor = null
        result.FinishInfo = new FinishInfoModel()
        result.FinishInfo.Players = [new FinishInfoPlayer(), new FinishInfoPlayer()]
        return result
    }
}

