import {Injectable} from 'angular2/core'
import {Http} from 'angular2/http'
import {ConfigProvider} from './ConfigProvider'


@Injectable()
export class UsersProvider {

    private cache = {}

    constructor(private config: ConfigProvider, private http: Http) {
    }

    private static Items = {}


    getCurrent(cb: (user: JokUser) => void) {
        var sid = this.config.sid
        var url = this.config.apiUrl + '/user/infobysid/' + sid + '?getBlockedUserIds=true&gameid=' + this.config.gameId + '&sid=' + this.config.sid

        this.http.get(url)
            .map(x => x.json())
            .subscribe((data: JokUser) => {
                if (data && data.UserID)
                    this.cache[data.UserID] = data
                cb(data)
            })
    }

    get(userid, cb: (user: JokUser) => void, forceNew = false) {

        var cacheItem = this.cache[userid]

        if (!forceNew && cacheItem) {
            cb(cacheItem)
            return
        }


        var url = this.config.apiUrl + '/user/info/' + userid + '?gameid=' + this.config.gameId + '&sid=' + this.config.sid
        this.http.get(url)
            .map(x => x.json())
            .subscribe((data: JokUser) => {
                this.cache[userid] = data
                cb(data)
            })
    }

    block(userid: number) {
        var url = this.config.apiUrl + '/user/block/' + userid + '?gameid=' + this.config.gameId + '&sid=' + this.config.sid
        this.http.post(url, null).subscribe()
    }

    unblock(userid: number) {
        var url = this.config.apiUrl + '/user/unblock/' + userid + '?gameid=' + this.config.gameId + '&sid=' + this.config.sid
        this.http.post(url, null).subscribe()
    }

    sendFriendRequest(userid: number) {
        var url = this.config.apiUrl + '/User/SendFriendRequest/' + userid + '?sid=' + this.config.sid
        this.http.get(url).subscribe()
    }
}


export interface JokUser {
    IsSuccess: boolean
    UserID: number
    Nick: string
    GenderID: number
    LanguageID: number
    AvatarUrl: string
    IsVIP: boolean
    RelationStatusID: number
    RelationStatusID2: UserRelationStatuses
    Game: JokPlayer
    Error: string
    BlockedUserIds: number[]
}

interface JokPlayer {
    ID: number
    ID2: string
    GameID: number
    UserID: number
    IsOnline: boolean
    LastLoginDate: string
    GamerRaiting: number
    GameCount: number
    LeaveCount: number
    Place1Count: number
    Place2Count: number
    Place3Count: number
    Place4Count: number
    OldCreateDate: Date
    CreateDate: Date
    Rating: number
    WinnerRatingCoeficient: number
    LoserRatingCoeficient: number
    Raiting2: number
    Rank: number
    LastDateGameCount: number
    IsProvisionalRating: boolean
    ProvisionalGameCount: number
    FromLevelName: string
    ToLevelName: string
    FromRating: number
    ToRating: number
    CupCountGold: number
    CupCountSilver: number
    CupCountBronze: number
    RatingStars: number
}

export enum UserRelationStatuses {
    New = 1,
    Accepted = 2,
    Rejected = 3,
    Pending = 4,
    Removed = 5
}