import {Component, Input, ElementRef, OnInit} from 'angular2/core'
import {NgClass} from 'angular2/common';

import {ClipboardDirective} from '../../Common/clipboard'

import {EmotionsPipe} from './emotions.pipe'
import {ChatItem} from './chat.model'
import {ChatProvider} from './chat.provider'
import {UIChatSmiles} from './chatsmiles'

declare var $: any;


@Component({
    host: {
        '(document:keyup)': 'onGlobalKeyUp($event)',
        '(document:click)': 'onGlobalClick($event)',
    },
    selector: 'chatbox',
    styles: [`/*#0B9A49;*/.chatbox {  width: 100%;  height: 100%;  position: absolute;  overflow-y: scroll;  overflow-x: hidden; }  .chatbox input.new-message {    width: 100%;    padding: 10px 15px;    padding-right: 30px;    color: white;    font-size: 14px;    background: transparent;    -moz-transition: all linear 70ms;    -o-transition: all linear 70ms;    -webkit-transition: all linear 70ms;    transition: all linear 70ms;    border-radius: 100px;    border: 1px solid #0B9A49; }    .chatbox input.new-message::-webkit-input-placeholder {      /* WebKit, Blink, Edge */      color: rgba(255, 255, 255, 0.28); }    .chatbox input.new-message:-moz-placeholder {      /* Mozilla Firefox 4 to 18 */      color: rgba(255, 255, 255, 0.28);      opacity: 1; }    .chatbox input.new-message::-moz-placeholder {      /* Mozilla Firefox 19+ */      color: rgba(255, 255, 255, 0.28);      opacity: 1; }    .chatbox input.new-message:-ms-input-placeholder {      /* Internet Explorer 10-11 */      color: rgba(255, 255, 255, 0.28); }    .chatbox input.new-message:placeholder-shown {      /* Standard (https://drafts.csswg.org/selectors-4/#placeholder) */      color: rgba(255, 255, 255, 0.28); }    .chatbox input.new-message:hover:not(:disabled), .chatbox input.new-message:focus:not(:disabled) {      border: 1px solid #027936;      background: #45BD79; }  .chatbox chatsmiles {    bottom: 100%;    position: absolute;    left: 9px;    z-index: 2000; }.chat-area .chat {  border-top-right-radius: 5px;  border-bottom-right-radius: 5px;  color: #434651; }  .chat-area .chat .emptyheight {    min-height: 310px; }  .chat-area .chat .chat-history {    padding: 23px 15px 30px;    min-height: 310px; }    .chat-area .chat .chat-history ul {      padding: 0;      margin: 0;      list-style: none; }    .chat-area .chat .chat-history .message-data {      margin-bottom: 10px; }      .chat-area .chat .chat-history .message-data > i {        display: none; }    .chat-area .chat .chat-history .message-data-name {      color: white; }    .chat-area .chat .chat-history .message-data-time {      color: white;      padding-left: 6px;      display: none; }    .chat-area .chat .chat-history li.is-first {      margin-top: 20px; }    .chat-area .chat .chat-history li .message {      box-shadow: 0 0 2px rgba(0, 0, 0, 0.39);      color: white;      padding: 12px 20px;      line-height: 26px;      border-radius: 7px;      margin-bottom: 5px;      max-width: 90%;      position: relative;      font-size: 14px;      background: #45BD79;      width: auto; }      .chat-area .chat .chat-history li .message.is-first {        /*&:after {                        bottom: 100%;                        left: 25px;                        border: solid transparent;                        content: " ";                        height: 0;                        width: 0;                        position: absolute;                        pointer-events: none;                        border-bottom-color: $green;                        border-width: 10px;                        margin-left: -10px;                    }*/ }        .chat-area .chat .chat-history li .message.is-first:after {          content: '';          position: absolute;          border-style: solid;          border-width: 0 6px 7px;          border-color: #45BD79 transparent;          display: block;          width: 0;          z-index: 1;          top: -7px;          left: 15px; }        .chat-area .chat .chat-history li .message.is-first:before {          content: '';          position: absolute;          border-style: solid;          border-width: 0 6px 7px;          border-color: #229653 transparent;          display: block;          width: 0;          z-index: 0;          top: -8px;          left: 15px; }    .chat-area .chat .chat-history li.mode_0 img {      width: 40px;      margin-bottom: 5px; }    .chat-area .chat .chat-history li.mode_0 .input-group {      margin-top: 10px; }    .chat-area .chat .chat-history li.mode_0 .share-items {      margin-top: 15px;      text-align: center; }      .chat-area .chat .chat-history li.mode_0 .share-items a {        color: white;        font-size: 28px;        margin-left: 4px;        margin-right: 4px;        -moz-transition: .1s;        -o-transition: .1s;        -webkit-transition: .1s;        transition: .1s; }        .chat-area .chat .chat-history li.mode_0 .share-items a:hover {          color: #f3f3f3; }    .chat-area .chat .chat-history li.mode_0 .message-data {      text-align: right; }    .chat-area .chat .chat-history li.mode_0 .message {      box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);      background: #4389AB;      float: right;      text-align: center; }      .chat-area .chat .chat-history li.mode_0 .message.is-first:before {        left: auto;        right: 12px; }      .chat-area .chat .chat-history li.mode_0 .message.is-first:after {        left: auto;        right: 12px;        border-bottom-color: #4389AB; }    .chat-area .chat .chat-history li.mode_1 .message {      background: #45BD79;      float: left; }      .chat-area .chat .chat-history li.mode_1 .message.is-first:after {        border-bottom-color: #45BD79; }    .chat-area .chat .chat-history li.mode_2 .message-data,    .chat-area .chat .chat-history li.mode_3 .message-data,    .chat-area .chat .chat-history li.mode_4 .message-data {      text-align: right; }    .chat-area .chat .chat-history li.mode_2 .message,    .chat-area .chat .chat-history li.mode_3 .message,    .chat-area .chat .chat-history li.mode_4 .message {      background: #45BD79;      float: right; }      .chat-area .chat .chat-history li.mode_2 .message.is-first:before,      .chat-area .chat .chat-history li.mode_3 .message.is-first:before,      .chat-area .chat .chat-history li.mode_4 .message.is-first:before {        left: auto;        right: 12px; }      .chat-area .chat .chat-history li.mode_2 .message.is-first:after,      .chat-area .chat .chat-history li.mode_3 .message.is-first:after,      .chat-area .chat .chat-history li.mode_4 .message.is-first:after {        left: auto;        right: 12px;        border-bottom-color: #45BD79; }.chat-area .chat-message {  padding: 20px 15px;  padding-top: 0;  position: relative; }  .chat-area .chat-message.is-fixed {    position: absolute;    left: 0;    right: 0;    bottom: 0; }  .chat-area .chat-message .sendemotion {    position: absolute;    right: 0;    top: 0;    font-size: 20px;    color: #60C38A;    cursor: pointer;    padding: 4px 23px 4px 23px; }    .chat-area .chat-message .sendemotion:hover {      color: #34495E; }  .chat-area .chat-message input.disabled {    display: none; }  .chat-area .chat-message.emotions_only input {    display: none; }  .chat-area .chat-message.emotions_only .sendemotion {    right: 38%; }  .chat-area .chat-message.emotions_only input.disabled {    display: block; }.clearfix:after {  visibility: hidden;  display: block;  font-size: 0;  content: " ";  clear: both;  height: 0; }`],
    template:'<div class="chatbox ios-scroll"><div class="chat-area"><div class="chat"><div class="chat-history"><ul><li *ngFor="#item of source" [ngSwitch]="item.Template" class="clearfix mode_{{item.Mode}} {{item.ShowHeader ? \'is-first\' : \'\'}}"><div *ngSwitchWhen="0"><div *ngIf="item.ShowHeader" class="message-data"><span class="message-data-time">{{item.Timestamp}}</span> <span class="message-data-name">{{item.Nick}}</span> <i class="fa fa-circle me"></i></div><div class="message {{item.ShowHeader ? \'is-first\' : \'\'}}"><div *ngFor="#message of item.Messages" [innerHTML]="message | emotions"></div></div></div><div *ngSwitchWhen="1"><div *ngIf="item.ShowHeader" class="message-data"><span class="message-data-time">{{item.Timestamp}}</span> <span class="message-data-name">{{item.Nick}}</span> <i class="fa fa-circle me"></i> <img [src]="jokImageUrl"></div><div class="message {{item.ShowHeader ? \'is-first\' : \'\'}}">{{ \'chatbox.PublicTableTitle\' | translate | emotions }}</div></div><div *ngSwitchWhen="2"><div *ngIf="item.ShowHeader" class="message-data"><span class="message-data-time">{{item.Timestamp}}</span> <span class="message-data-name">{{item.Nick}}</span> <i class="fa fa-circle me"></i> <img [src]="jokImageUrl"></div><div class="message {{item.ShowHeader ? \'is-first\' : \'\'}}"><div [innerHtml]="\'chatbox.PrivateTableTitle\' | translate | emotions"></div><div class="input-group"><input #shareTableLink type="text" class="form-control" [value]="item.Messages[0]" readonly id="ShareTableLink"> <span class="input-group-btn"><button class="btn btn-default" type="button" clipboard data-clipboard-target="#ShareTableLink" data-toggle="tooltip" data-placement="top">{{ \'common.Copy\' | translate }}</button></span></div><div class="share-items"><a href="javascript:void(0)" *ngIf="false"><i class="fa fa-facebook-square"></i></a> <a href="javascript:void(0)" (click)="shareTwitter(shareTableLink.value)"><i class="fa fa-twitter-square"></i></a> <a href="javascript:void(0)" *ngIf="false"><i class="fa fa-odnoklassniki-square"></i></a></div></div></div><div *ngSwitchWhen="3"><div *ngIf="item.ShowHeader" class="message-data"><span class="message-data-time">{{item.Timestamp}}</span> <span class="message-data-name">{{item.Nick}}</span> <i class="fa fa-circle me"></i></div><div class="message {{item.ShowHeader ? \'is-first\' : \'\'}}">{{item.Messages[0]}} let\'s be friends<br></div></div><div *ngSwitchWhen="4"><div *ngIf="item.ShowHeader" class="message-data"><span class="message-data-time">{{item.Timestamp}}</span> <span class="message-data-name">{{item.Nick}}</span> <i class="fa fa-circle me"></i> <img [src]="jokImageUrl"></div><div class="message {{item.ShowHeader ? \'is-first\' : \'\'}}">{{ \'chatbox.GameStarting\' | translate : { seconds: gameStartingSeconds } }}</div></div></li></ul></div><div class="chat-message clearfix" [class.is-fixed]="isMessageBoxFixed" [class.emotions_only]="onlyEmotions"><input type="text" class="form-control new-message" [(ngModel)]="messageText" [placeholder]="\'chatbox.TypeMessageHere\' | translate" (keyup)="onKeyUp($event)"> <input type="text" class="form-control new-message disabled" disabled><div class="sendemotion" data-toggle="tooltip" data-placement="top" [title]="\'chatbox.SendEmotion\' | translate" (click)="onSmilesClick()"><i class="fa fa-smile-o"></i></div><chatsmiles *ngIf="showSmiles" [isRightAligned]="!onlyEmotions" [isVIPMember]="isVIPMember"></chatsmiles></div></div></div></div>',
    directives: [NgClass, ClipboardDirective, UIChatSmiles],
    pipes: [EmotionsPipe]
})
export class UIChatBox implements OnInit {

    @Input()
    gameStartingSeconds: number

    @Input()
    isVIPMember: boolean

    @Input()
    onlyEmotions: boolean


    showSmiles: boolean

    source: ChatItem[]

    jokImageUrl = '/Images/harley.png'
    //jokImageUrl = 'https://jok.io/portal/avatar/~base_skin_07a~Clothes_06~Eyes_02~Mouth_08~~Facialhair_06~Hair_07~Accessories_08~8588247141691673994'

    messageText: string

    isMessageBoxFixed: boolean = true


    constructor(private elem: ElementRef, private chat: ChatProvider) {
        this.source = chat.Items
    }

    ngOnInit() {
        this.chat.on('HideSmilesBox', () => this.showSmiles = false)
        this.chat.on('ItemAdded', () => { this.refreshMessageBoxFixed(); this.scrollToBottom(); })
        this.chat.on('Refresh', () => { this.refreshMessageBoxFixed(); this.scrollToBottom(); })
        this.chat.on('SendMessage', () => { this.refreshMessageBoxFixed(); this.scrollToBottom(); })
        this.refreshMessageBoxFixed()
    }


    onGlobalClick(ev: MouseEvent) {
        //this.showSmiles = false
    }

    onGlobalKeyUp(ev: KeyboardEvent) {
        this.showSmiles = false

        if (ev.keyCode == 13 /*Enter*/ || ev.keyCode == 67 /*c*/) {
            $(this.elem.nativeElement).find('.new-message').focus()
            return
        }

        if (ev.keyCode == 27 /*Esc*/) {
            return
        }
    }

    onKeyUp(ev: KeyboardEvent) {
        if (ev.keyCode == 13 /*Enter*/) {
            if (!this.messageText) {
                $(this.elem.nativeElement).find('.new-message').blur()
                return
            }

            this.sendMessage(this.messageText)
            this.messageText = '';
            return
        }

        if (ev.keyCode == 27 /*Esc*/) {
            this.messageText = '';
            $(this.elem.nativeElement).find('.new-message').blur()
            this.showSmiles = false
            return
        }

    }

    onSmilesClick() {
        this.showSmiles = !this.showSmiles
    }

    refreshMessageBoxFixed() {
        this.isMessageBoxFixed = $(this.elem.nativeElement).find('.chat-history').outerHeight() < $(window).height() - 100
    }

    shareTwitter(shareUrl) {

        var text = '#jokchellenge';
        var url = "https://twitter.com/intent/tweet?via=JokEntertainers&text=" + encodeURIComponent(text) + "&source=tweetbutton&url=" + encodeURIComponent(shareUrl);


        var _openWindow = function (url) {
            var g = 640;
            var m = 460;
            var f = window.screen.height / 2 - (m / 2);
            var i = window.screen.width / 2 - (g / 2);
            var l = window.open(url, "Share", "status=1,height=" + m + ",width=" + g + ",top=" + f + ",left=" + i + ",resizable=0")
        }

        _openWindow(url);
    }


    private sendMessage(msg) {
        this.chat.sendMessage(msg)
        this.scrollToBottom()
        this.refreshMessageBoxFixed()
    }

    private scrollToBottom() {
        try {
            var $el = $(this.elem.nativeElement).find('.chatbox')

            $($el).stop()
            $($el).animate({ scrollTop: $el[0].scrollHeight })

        } catch (err) { }
    }
}
