import {Injectable} from 'angular2/core'
import {Http} from 'angular2/http'

import {ConfigProvider} from '../../Common/Providers/All'

import {Channel, GetChannelsResult} from './music.model'


@Injectable()
export class MusicProvider extends EventEmitter {

    static PLAY = 'PLAY'
    static STOP = 'STOP'
    static STARTED_PLAYING = 'STARTED_PLAYING'
    static FAILED_PLAYING = 'FAILED_PLAYING'


    public channels: Channel[] = []
    public activeChannel: Channel
    public isPlaying: boolean
    public searchText: string = ''
    public channelGroups: Channel[][] = [[], [], []]

    private audioStream: HTMLAudioElement;

    
    constructor(private config: ConfigProvider, private http: Http) {
        super()

        this.http.get(this.config.apiUrl + '/music/channels/all?sid=' + this.config.sid).subscribe((response) => {
            var res: GetChannelsResult = response.json()
            if (!res.IsSuccess) return

            res.Data.forEach(x => x.Name = x.Name.toUpperCase())

            this.channels = res.Data

            this.refreshChannels()
            this.loadLastListeningChannel()
        })


        this.audioStream = new Audio()
        this.audioStream.addEventListener('playing', this.audioPlayingStarted.bind(this), false)
        this.audioStream.addEventListener('error', this.audioPlayingError.bind(this), false)

        this.refreshChannels()
        this.stop();
    }

    
    public play(channelName?: string) {
        if (!this.channels || !this.channels.length) {
            console.warn('no channels')
            return;
        }

        this.isPlaying = true

        this.activeChannel = this.channels.filter(x => x.Name == channelName)[0] || this.activeChannel || this.channels[0];
        this.playStream(this.activeChannel.Source);

        localStorage.setItem('music_channel', this.activeChannel.Name.toUpperCase())

        this.emit(MusicProvider.PLAY, this.activeChannel)
    }

    public stop() {

        this.isPlaying = false

        this.audioStream.pause();
        try {
            this.audioStream.src = '';
        }
        catch (err) { }

        this.emit(MusicProvider.STOP)
    }

    public nextChannel() {
        if (!this.channels || !this.channels.length) return;

        var index = this.channels.indexOf(this.activeChannel);
        this.activeChannel = this.channels[index + 1 >= this.channels.length ? 0 : ++index];

        this.play(this.activeChannel.Name);
    }

    public prevChannel() {
        if (!this.channels || !this.channels.length) return;

        var index = this.channels.indexOf(this.activeChannel);
        this.activeChannel = this.channels[index - 1 < 0 ? this.channels.length - 1 : --index];

        this.play(this.activeChannel.Name);
    }

    public getChannelName(id: number) {
        var channel = this.channels.filter(x => x.ID == id)[0]
        return channel ? channel.Name : null
    }


    private refreshChannels() {

        var favorites = this.channels.filter(x => x.IsFavorite && !x.IsOffline && this.search(x, this.searchText))
        var featured = this.channels.filter(x => x.IsFeatured && !x.IsOffline && this.search(x, this.searchText))
        var other = this.channels.filter(x => !x.IsFavorite && !x.IsFeatured && !x.IsOffline && this.search(x, this.searchText))

        this.channelGroups = [
            favorites,
            featured,
            other
        ]
    }

    private search(channel: Channel, text: string): boolean {
        if (!text) return true

        text = text.toString().toUpperCase()

        if (~channel.Name.indexOf(text)) return true

        if (~channel.TrackInfo.indexOf(text)) return true

        return false
    }

    private audioPlayingStarted() {
        this.emit(MusicProvider.STARTED_PLAYING)
    }

    private audioPlayingError() {
        this.emit(MusicProvider.FAILED_PLAYING)
    }

    private loadLastListeningChannel() {
        var isMusicPlaying = localStorage.getItem('isMusicPlaying');
        var channel = localStorage.getItem('music_channel');

        if (isMusicPlaying != '1') {

            if (channel)
                this.activeChannel = this.channels.filter(c => c.Name.toUpperCase() == channel)[0];

            return;
        }

        this.play(channel);
    }

    private playStream(url: string) {
        this.audioStream.src = url;
        this.audioStream.play();
        this.audioStream.volume = 0.6;
    }
}
