
import {CardColor, CardLevel} from '../Enums'


export class Card_Type {

    public ID: string;
    public Value: number;
    public Color: CardColor;
    public Level: CardLevel;
    public Avable: boolean;
    public LeftClick: boolean;
    public Special: CardColor;
    public IsJoker: boolean;

    public Clone() {
        var item = new Card_Type();
        item.ID = this.ID;
        item.Value = this.Value;
        item.Color = this.Color;
        item.Level = this.Level;
        item.Avable = this.Avable;
        item.LeftClick = this.LeftClick;
        item.Special = this.Special;
        item.IsJoker = this.IsJoker;

        return item;
    }
}