import {Directive, ElementRef} from 'angular2/core'

declare var $: any

@Directive({
    selector: '[hover]'
})
export class HoverDirective {
    constructor(el: ElementRef) {

        var $el = $(el.nativeElement)

        $el.on('touchstart', (e) => {
            e.stopPropagation();

            if (!$el.hasClass('hover'))
                $el.addClass('hover')
        })
        $el.on('touchend', (e) => {
            e.stopPropagation();

            $el.removeClass('hover')
        })

        $el.on('touchcancel', (e) => {
            e.stopPropagation();

            $el.removeClass('hover')
        })


        //$el.mouseenter((e) => {
        //    console.log('mouseenter')
        //    if (!$el.hasClass('hover'))
        //        $el.addClass('hover')
        //})
        //$el.mouseleave((e) => {
        //    console.log('mouseleave')
        //    $el.removeClass('hover')
        //})
    }
}