import {Injectable, NgZone, OnInit} from 'angular2/core'
import {Title}  from 'angular2/platform/browser'
import {Router} from 'angular2/router'
import {TranslateService} from 'ng2-translate'

import {CommunicationClientProvider} from 'Jok/Communication'
import {CardType, CardColor, CardLevel, ColorSelectionMode, DownCardsProvider} from 'Jok/Cards'
import {ChatItemMode, ChatItemTemplate, ChatProvider} from 'Jok/Chat'
import {PlatformProvider} from 'Jok/Platforms'

import {ConfigProvider, ProfileProvider, UsersProvider, UserRelationStatuses, CurrentUserProvider} from '../../Common/Providers/All'

import {ViewModelProvider, ViewModel} from './viewmodel.provider'
import {GameTable, GamePlayer, NotificationType, TableStatuses, PlayAudioModes, ServerCardsPair, ServerCard, ServerCardColor, CardPermission, FinishResult} from './game.model'
import {FinishInfoModel, FinishInfoPlayer} from './finishinfo.model'


@Injectable()
export class GameProvider {

    CHAT_SENDER_JOK = ' '

    VM: ViewModel

    lastCard: CardType
    tableId: string
    lastPlayersCount: number

    private tableResetReceived: boolean
    private startCountDownInterval: any
    private blockedUserIds: number[]

    private isActivated: boolean
    private specialColor: CardColor


    constructor(
        private router: Router,
        private viewModelProvider: ViewModelProvider,
        private translate: TranslateService,
        private config: ConfigProvider,
        private usersProvider: UsersProvider,
        private downCardsProvider: DownCardsProvider,
        private profileProvider: ProfileProvider,
        private server: CommunicationClientProvider,
        private currentUser: CurrentUserProvider,
        private chat: ChatProvider,
        private zone: NgZone,
        private windowTitle: Title,
        private platform: PlatformProvider
    ) {
        this.VM = viewModelProvider.VM

        this.blockedUserIds = []
    }


    // public API
    start() {
        this.VM.NotificationType = NotificationType.Authenticating
        this.VM.IsConnectionClosed = false

        this.chat.reset()

        this.usersProvider.getCurrent(user => {

            if (!user.IsSuccess) {
                this.serverRequireAuthorization()
                return
            }

            this.blockedUserIds = user.BlockedUserIds || []

            this.VM.CurrentPlayer.UserID = this.config.currentUserId = user.UserID
            this.VM.CurrentPlayer.Nick = user.Nick
            this.VM.CurrentPlayer.AvatarUri = user.AvatarUrl
            this.VM.CurrentPlayer.LevelName = this.currentUser.getPlayerLevelName(user.Game.RatingStars)
            this.VM.CurrentPlayer.IsOnline = true

            this.VM.NotificationType = NotificationType.Connecting


            this.chat.configure(user.UserID, user.Nick, user.AvatarUrl)
            this.chat.on('ItemAdded', (message, userid, mode: ChatItemMode) => {
                if (!userid || mode == ChatItemMode.Jok) return

                this.profileProvider.chatBubble(message, userid)
            })

            this.connectServer()
        })
    }

    stop() {
        this.VM.IsConnectionClosed = true

        this.server.stop()
        this.server.removeEvent()
    }

    connectServer() {

        this.startWindowTitleNotification()

        this.server.setOptions({
            hubName: 'GameHub',
            url: 'https://jok.io/JapMatrixServer/signalr',
            token: this.config.sid,
            channel: this.config.channel,
            gameMode: this.config.mode,
            levelControl: 0,
            starsControl: 0,
            transports: ['longPolling'],
            enableMessageLogging: true
        })

        this.server.on('MessageReceived', this.serverMessageReceived.bind(this))
        this.server.on('MessageProceedCompleted', this.serverMessageProceedCompleted.bind(this))
        this.server.on('online', this.serverOnline.bind(this))
        this.server.on('offline', this.serverOffline.bind(this))
        this.server.on('Close', this.serverClose.bind(this))
        this.server.on('GameStarting', this.serverGameStarting.bind(this))
        this.server.on('GameFinished', this.serverGameFinished.bind(this))
        this.server.on('CurrentTable', this.serverCurrentTable.bind(this))
        this.server.on('UserCards', this.serverUserCards.bind(this))
        this.server.on('KozirRequest', this.serverKozirRequest.bind(this))
        this.server.on('UserCardPermissions', this.serverUserCardPermissions.bind(this))
        this.server.on('CardRequest', this.serverCardRequest.bind(this))
        this.server.on('UserCardDown', this.serverUserCardDown.bind(this))
        this.server.on('OpenUserCard', this.serverOpenUserCard.bind(this))
        this.server.on('HideOpponentPair', this.serverHideOpponentPair.bind(this))
        this.server.on('UserSelectedKozir', this.serverUserSelectedKozir.bind(this))
        this.server.on('UserTook', this.serverUserTook.bind(this))
        this.server.on('ActivateUser', this.serverActivateUser.bind(this))
        this.server.on('CardDownNotification', this.serverCardDownNotification.bind(this))
        this.server.on('UpdateUserOnlineStatus', this.serverUpdateUserOnlineStatus.bind(this))
        this.server.on('OpponentCardsCount', this.serverOpponentCardsCount.bind(this))
        this.server.on('GameFinishResult', this.serverGameFinishResult.bind(this))
        this.server.on('Buzz', this.serverBuzz.bind(this))
        this.server.on('ChatMessage', this.serverChatMessage.bind(this))
        this.server.on('UserListeningMusic', this.serverUserListeningMusic.bind(this))

        this.server.start()

        this.chat.on('SendMessage', (msg) => this.server.send('ChatMessage', msg))
    }

    blockUser(userid: number) {
        this.usersProvider.block(userid)

        if (this.blockedUserIds.indexOf(userid) > -1) return

        this.blockedUserIds.push(userid)
    }

    unblockUser(userid: number) {
        this.usersProvider.unblock(userid)

        var index = this.blockedUserIds.indexOf(userid)
        if (index == -1) return

        this.blockedUserIds.splice(index, 1)
    }

    sendFriendRequest(userid: number) {
        this.usersProvider.sendFriendRequest(userid)

        if (this.VM.OpponentPlayer && this.VM.OpponentPlayer.UserID == userid)
            this.VM.OpponentPlayer.IsFrendRequestSent = true

        this.usersProvider.get(userid, user =>
            this.translate.get('common.BeFriends').subscribe(text => {
                this.chat.sendMessage(user.Nick + ' ' + text)
            })
        )
    }



    // UI Callbacks
    uiCardClick(card: CardType) {

        if (CardType.IsSpecial(card)) {
            this.lastCard = card
            this.VM.ColorSelectionMode = ColorSelectionMode.WantTakeSelection
            return
        }

        var index = this.VM.CurrentPlayerCards.indexOf(card)
        if (~index)
            this.VM.CurrentPlayerCards[index].IsVisible = false

        this.cardSelected(card)
    }

    uiDominatedColorSelect(dominatedColor: CardColor) {
        this.VM.ColorSelectionMode = null
        this.VM.DominatedColor = dominatedColor
        this.VM.CurrentPlayerCards.filter(x => x.CardColor == dominatedColor).forEach(x => x.IsDominated = true)

        this.VM.ActivePlayerUserID = null
        this.profileProvider.stopActivate()

        this.server.send('SelectKozir', dominatedColor)
    }

    uiWantTakeClick(isWant: boolean) {
        if (this.VM.IsFirstCard) {
            this.VM.ColorSelectionMode = isWant ? ColorSelectionMode.WantColorSelection : ColorSelectionMode.TakeColorSelection
            return
        }

        var index = this.VM.CurrentPlayerCards.indexOf(this.lastCard)
        if (~index)
            this.VM.CurrentPlayerCards[index].IsVisible = false

        this.lastCard.IsActivated = isWant

        this.cardSelected(this.lastCard, isWant)
    }

    uiWantColorSelect(wantColor: CardColor) {

        var index = this.VM.CurrentPlayerCards.indexOf(this.lastCard)
        if (~index)
            this.VM.CurrentPlayerCards[index].IsVisible = false


        this.lastCard.SpecialColor = wantColor
        this.lastCard.IsActivated = true

        this.cardSelected(this.lastCard, true, wantColor)

    }

    uiTakeColorSelect(takeColor: CardColor) {

        var index = this.VM.CurrentPlayerCards.indexOf(this.lastCard)
        if (~index)
            this.VM.CurrentPlayerCards[index].IsVisible = false

        this.lastCard.SpecialColor = takeColor
        this.lastCard.IsActivated = false

        this.cardSelected(this.lastCard, false, takeColor)
    }

    uiListenChannel(channel: string) {

    }

    uiPlayNewGame() {
        document.location.reload()
    }

    uiExit() {
        this.router.navigate(['/Lobby'])
    }



    // Server Callbacks
    private serverMessageReceived(pkg) {
    }

    private serverMessageProceedCompleted(data: string[]) {
        console.log(data)
    }

    private serverOnline() {
        console.log('game server: connected')
        this.VM.NotificationType = null
    }

    private serverOffline() {
        console.log('game server: disconnected')
        this.serverClose('Connection lost', true)
    }

    private serverClose(reason, skipClosing) {
        this.VM.NotificationType = NotificationType.ConnectionClosed

        if (skipClosing) return

        this.VM.ConnectionCloseReason = reason
        this.stop()
    }

    private serverRequireAuthorization() {
        this.VM.NotificationType = NotificationType.RequireAuthorization

        this.platform.login()
    }

    private serverGameStarting() {

        this.chat.removeJokItems()
        this.startWindowTitleNotification()

        this.VM.CurrentPlayer.TakenCount = 0
        if (this.VM.OpponentPlayer)
            this.VM.OpponentPlayer.TakenCount = 0
    }

    private serverGameFinished() {
        this.stop()
        this.VM.IsConnectionClosed = this.VM.NotificationType = null

        setTimeout(() => {
            this.VM.CurrentPlayer.Want = null
            this.VM.OpponentPlayer.Want = null
        }, 2000)
    }

    private serverCurrentTable(table: GameTable, channel) {

        this.tableId = table.ID
        this.VM.Status = table.Status
        this.VM.TableChannel = table.Channel
        this.VM.IsTableInfoReceived = true
        this.VM.NotificationType = null

        this.VM.OpponentPlayer = null


        if (!this.lastPlayersCount || (this.lastPlayersCount < table.Players.length)) {
            this.playAudio(PlayAudioModes.PlayerLogin)
        }
        this.lastPlayersCount = table.Players.length



        if (table.Players.length > 1) {
            var opponentUser = table.Players.filter(x => x.UserID != this.VM.CurrentPlayer.UserID)[0]
            if (opponentUser) {
                this.VM.OpponentPlayer = new GamePlayer()
                this.VM.OpponentPlayer.UserID = opponentUser.UserID
                this.VM.OpponentPlayer.IsOnline = opponentUser.IsOnline

                this.usersProvider.get(opponentUser.UserID, user => {
                    this.VM.OpponentPlayer.Nick = user.Nick
                    this.VM.OpponentPlayer.AvatarUri = user.AvatarUrl
                    this.VM.OpponentPlayer.LevelName = user.Game ? this.currentUser.getPlayerLevelName(user.Game.RatingStars) : ''
                    this.VM.OpponentPlayer.RelationStatusID = this.VM.OpponentPlayer.IsFrendRequestSent ? UserRelationStatuses.Pending : user.RelationStatusID2
                    this.VM.OpponentPlayer.IsBlocked = this.blockedUserIds.indexOf(user.UserID) > -1
                })
            }
        }


        switch (this.VM.Status) {

            case TableStatuses.New: {

                if (table.Channel) {
                    this.VM.InviteLink = this.config.playUrl + '?channel=' + table.Channel
                    this.chat.addItem(ChatItemMode.Jok, this.CHAT_SENDER_JOK, this.VM.InviteLink, ChatItemTemplate.ServerPrivateTable, null, null, null)
                }
                else {
                    this.chat.addItem(ChatItemMode.Jok, this.CHAT_SENDER_JOK, this.VM.InviteLink, ChatItemTemplate.ServerPublicTable, null, null, null)
                }

                break
            }

            case TableStatuses.Started:
            case TableStatuses.Stopped: {

                break
            }

            case TableStatuses.Finished: {

                var users = table.Players;

                var player1 = users[0].UserID == this.config.currentUserId ? users[0] : users[1];
                var player2 = users[0].UserID == this.config.currentUserId ? users[1] : users[0];

                var areBothOnline = player1.IsOnline && player2.IsOnline;

                this.VM.DownCards = null
                this.VM.DominatedColor = null


                var player1Place = player1.Score > player2.Score ? 1 : 2
                var player2Place = player2.Score > player1.Score ? 1 : 2

                if (player1.Score == player2.Score) {
                    player1Place = player2Place = 0
                }

                var winnerPoints = this.VM.FinishInfo.Players[0].Points

                this.VM.FinishInfo = new FinishInfoModel()
                this.VM.FinishInfo.Players = [
                    {
                        UserID: this.VM.CurrentPlayer.UserID,
                        Nick: this.VM.CurrentPlayer.Nick,
                        Score: player1.Score,
                        Points: winnerPoints,
                        Rating: 0,
                        IsOnline: this.VM.CurrentPlayer.IsOnline,
                        Place: player1Place
                    },
                    {
                        UserID: this.VM.OpponentPlayer.UserID,
                        Nick: this.VM.OpponentPlayer.Nick,
                        Score: player2.Score,
                        Points: 0,
                        Rating: 0,
                        IsOnline: this.VM.OpponentPlayer.IsOnline,
                        Place: player2Place
                    }
                ]


                //this.usersProvider.get(player1.UserID, (currentPlayerInfo) => {
                //    this.usersProvider.get(player2.UserID, (opponentPlayerInfo) => {

                //        var winnerNick = (table.LastWinnerUserID == currentPlayerInfo.UserID) ? currentPlayerInfo.Nick : opponentPlayerInfo.Nick;

                //        //var notificationViewName = (player1.TakenCount == player2.TakenCount) ? 'draw_result_play_again' : (areBothOnline ? 'winner_result_play_again' : 'winner_result_new_game');
                //        //this.game.notification.show(notificationViewName,
                //        //    // Current Player
                //        //    currentPlayreAvatar,
                //        //    currentPlayerInfo.Nick,
                //        //    player1.Score,
                //        //    player1.TakenCount,
                //        //    player1.TakenCount * 10,
                //        //    '{Rating}',

                //        //    // Opponent Player
                //        //    opponentAvatar,
                //        //    opponentPlayerInfo.Nick,
                //        //    player2.Score,
                //        //    player2.TakenCount,
                //        //    player2.TakenCount * 10,
                //        //    '{Rating}',

                //        //    // Winner
                //        //    winnerNick
                //        //);
                //    });
                //});
                break
            }
        }
    }

    private serverUserCards(cards: ServerCard[], cardPairs: ServerCardsPair[], opponentOpenCards: ServerCard[]) {

        this.VM.CurrentPlayerCards = cards.map(x => this.convertServerCard(x)).filter(x => x.IsVisible).sort(CardType.Sort)
        this.VM.CurrentPlayerOpenCards = cardPairs.map(x => this.convertServerCard(x.OpenCard)).filter(x => x.IsVisible).sort(CardType.SortWithDominatedLast)
        this.VM.OpponentPlayerOpenCards = opponentOpenCards.map(x => this.convertServerCard(x)).filter(x => x.IsVisible).sort(CardType.SortWithDominatedLast)
    }

    private serverKozirRequest() {
        this.VM.DominatedColor = null
        this.VM.ColorSelectionMode = ColorSelectionMode.ColorSelection

        this.startWindowTitleNotification()
    }

    private serverUserCardPermissions(permissions: CardPermission[]) {
        if (!permissions || !permissions.length) return

        permissions.forEach(p => {

            var card = this.VM.CurrentPlayerOpenCards.filter(x => x.ID == p.CardID)[0]
            if (!card)
                card = this.VM.CurrentPlayerCards.filter(x => x.ID == p.CardID)[0]

            if (!card) return

            card.IsEnabled = p.IsEnabled
        })
    }

    private serverCardRequest(IsFirstCard: boolean) {
        this.VM.IsFirstCard = IsFirstCard
        this.VM.IsCardSelectionEnabled = true
    }

    private serverUserCardDown(userid, card: ServerCard) {

        var isOpponentCard = (this.config.currentUserId != userid);
        var searchCard = null


        if (!isOpponentCard) {
            searchCard = this.VM.CurrentPlayerCards.filter(x => x.ID == card.ID)[0]
            if (!searchCard)
                searchCard = this.VM.CurrentPlayerOpenCards.filter(x => x.ID == card.ID)[0]
        }
        else {
            searchCard = this.VM.OpponentPlayerOpenCards.filter(x => x.ID == card.ID)[0]
        }


        if (searchCard)
            searchCard.IsVisible = false



        this.removeFromCardsCollection(card.ID, this.VM.CurrentPlayerOpenCards)
        this.removeFromCardsCollection(card.ID, this.VM.OpponentPlayerOpenCards)

        var convertedCard = this.convertServerCard(card)

        if (CardType.IsSpecial(convertedCard)) {
            convertedCard.IsActivated = this.isActivated
            convertedCard.SpecialColor = this.specialColor
        }

        this.VM.DownCards[isOpponentCard ? 2 : 0] = convertedCard
        this.VM.LastActivePlayerPosition = isOpponentCard ? 2 : 0

        this.playAudio(CardType.IsSpecial(convertedCard) ? PlayAudioModes.SpecialCard : PlayAudioModes.Card)


        this.VM.ActivePlayerUserID = null
        this.VM.ColorSelectionMode = null //  
    }

    private serverOpenUserCard(cardid: string, card: ServerCard) {
        var newCard = this.convertServerCard(card)
        this.VM.CurrentPlayerCards.push(newCard)


        // TODO: 1    
        //if (this.game.currentOpenCards.items.filter(x => x.isVisible()).length == 1)
        //    this.game.currentOpenCards.hide();

        //if (this.game.opponentOpenCards.items.filter(x => x.isVisible()).length == 1)
        //    this.game.opponentOpenCards.hide();
    }

    private serverHideOpponentPair(cardid: string) {
        // TODO: Maybe nothing
    }

    private serverUserSelectedKozir(cardColor) {
        this.VM.ColorSelectionMode = null
        this.VM.DominatedColor = cardColor

        this.VM.CurrentPlayerCards.forEach(x => x.IsDominated = x.CardColor == cardColor)
        this.VM.CurrentPlayerOpenCards.forEach(x => x.IsDominated = x.CardColor == cardColor)
        this.VM.OpponentPlayerOpenCards.forEach(x => x.IsDominated = x.CardColor == cardColor)
    }

    private serverUserTook(userid: number, takenCount: number) {

        this.VM.IsFirstCard = true
        this.VM.ActivePlayerUserID = null
        this.VM.DownCardsAnimated = this.VM.LastTakenCards = this.VM.DownCards
        this.VM.DownCards = [null, null, null, null]

        var currentUserTook = this.config.currentUserId == userid

        this.downCardsProvider.animate(currentUserTook ? 1 : 3, () => {

            this.VM.DownCardsAnimated = null

            if (currentUserTook)
                this.VM.CurrentPlayer.Want = takenCount
            else
                this.VM.OpponentPlayer.Want = takenCount
        })
    }

    private serverActivateUser(userid) {
        var players = [this.VM.CurrentPlayer, this.VM.OpponentPlayer]
        var player = players.filter(x => x.UserID == userid)[0]
        if (!player) return

        this.VM.ActivePlayerUserID = player.UserID
        this.profileProvider.activate(player.UserID, (!player.IsOnline) ? 3000 : 15000)
    }

    private serverCardDownNotification(isActivated: boolean, specialColor: ServerCardColor) {
        this.isActivated = isActivated
        this.specialColor = specialColor - 0
    }

    private serverUpdateUserOnlineStatus(userid: number, isOnline: boolean) {

        if (userid == this.config.currentUserId) return;

        this.VM.OpponentPlayer.IsOnline = isOnline
    }

    private serverOpponentCardsCount(count: number) {
        this.VM.OpponentCardsCount = count
    }

    private serverGameFinishResult(result: FinishResult) {
        this.VM.FinishInfo.Players[0].Points = result.AddedPoints
    }

    private serverBuzz() {
        // TODO: Shake effect

        this.playAudio(PlayAudioModes.Buzz)

        setTimeout(() => this.profileProvider.chatBubble('Buzz', this.VM.OpponentPlayer.UserID), 2000)
    }

    private serverChatMessage(userid, msg, msgId) {

        if (this.blockedUserIds.indexOf(userid) > -1) return

        this.usersProvider.get(userid, user => {
            this.chat.addItem(ChatItemMode.player2, user.Nick, msg, ChatItemTemplate.Default, msgId, user.UserID, user.AvatarUrl)
        })
    }

    private serverChatBanned(days) {
        this.chat.addItem(ChatItemMode.Jok, this.CHAT_SENDER_JOK, 'Chat is blocked for {DAYS} days, reason: censorship. You can only use smiles.'.replace('{DAYS}', days));
    }

    private serverUserListeningMusic(userid, isListening, channelID) {

        if (this.config.currentUserId == userid) return

        this.VM.OpponentPlayer.MusicChannel = channelID
        this.VM.OpponentPlayer.IsListening = isListening
    }




    private cardSelected(card: CardType, isWant = false, wantColor = -1) {
        this.VM.ColorSelectionMode = null

        this.VM.DownCards[0] = card
        this.VM.DownCards[0].IsEnabled = false
        this.VM.IsCardSelectionEnabled = false
        this.VM.ActivePlayerUserID = null
        this.VM.LastActivePlayerPosition = 0

        this.profileProvider.stopActivate()

        this.removeFromCardsCollection(card.ID, this.VM.CurrentPlayerOpenCards)

        this.playAudio(CardType.IsSpecial(card) ? PlayAudioModes.SpecialCard : PlayAudioModes.Card)

        this.server.send('SelectCard', card.ID, isWant, wantColor)
    }

    private playAudio(mode: PlayAudioModes) {
        var audios = {
            cardDown: 'carddown',
            cardDown2: 'carddown2',
            specialCardDown: 'special_card_down',
            playerLogin: 'PlayerLoginNotification',
            Buzz: 'Buzz'
        }
        var sound

        if (!this.config.isAudioEffectsEnabled) return

        if (mode == PlayAudioModes.SpecialCard) {
            sound = audios.specialCardDown
        }

        if (mode == PlayAudioModes.Card) {
            if (Math.floor(Math.random() * 2) == 0)
                sound = audios.cardDown
            else
                sound = audios.cardDown2
        }

        if (mode == PlayAudioModes.PlayerLogin) {
            sound = audios.playerLogin
        }

        if (!sound) return

        this.platform.playAudio(sound)
    }

    private startWindowTitleNotification() {

        var originalWindowTitle = this.windowTitle.getTitle()
        var symbols = ['', '', '', '', '', '']

        var interval = setInterval(() => {
            if (!document.hidden) {
                this.windowTitle.setTitle(originalWindowTitle)
                clearInterval(interval)
                return
            }

            var symbol = symbols[Date.now() % symbols.length]

            var newTitle = ((this.windowTitle.getTitle() != originalWindowTitle) ? '' : symbol + ' ') + originalWindowTitle
            this.windowTitle.setTitle(newTitle)
        }, 300)
    }

    private convertServerCard(x: ServerCard): CardType {
        var card = new CardType()
        card.ID = x.ID
        card.CardColor = x.Color - 0
        card.CardLevel = x.Level - 6
        card.IsVisible = x.IsVisible
        card.IsDominated = card.CardColor == this.VM.DominatedColor
        card.IsEnabled = false

        if (x.Level == 15) {
            card.CardLevel = CardLevel._6
            card.CardColor = CardColor.Red
        }

        return card
    }

    private removeFromCardsCollection(cardid: string, cardsCollection: CardType[]) {

        var card = cardsCollection.filter(x => x.ID == cardid)[0]
        var openCardIndex = cardsCollection.indexOf(card)
        if (~openCardIndex) {
            cardsCollection.splice(openCardIndex, 1)

            if (cardsCollection.length == 1) {
                cardsCollection.splice(0, 1)
            }
        }

    }
}
