
export class CardType {
    public ID: string;
    public CardLevel: CardLevel;
    public CardColor: CardColor;
    public IsVisible: boolean;
    public IsEnabled: boolean;
    public IsDominated: boolean;

    // Magic J
    public SpecialColor: CardColor;
    public IsActivated: boolean;

    static Create(color, level, isEnabled = true) {
        var item = new CardType();
        item.CardColor = color;
        item.CardLevel = level;
        item.IsVisible = true;
        item.IsEnabled = isEnabled;
        item.IsDominated = false;

        return item;
    }

    static IsSpecial(card: CardType): boolean {
        var result = (card.CardColor == CardColor.Red || card.CardColor == CardColor.Purple) && (card.CardLevel == CardLevel._6)
        return result
    }

    static Sort(c1: CardType, c2: CardType) {
        var result = 10 * ((c2.CardColor + 1) * (c2.IsDominated ? 10 : 1) - (c1.CardColor + 1) * (c1.IsDominated ? 10 : 1)) + (c1.CardLevel - c2.CardLevel)


        if (CardType.IsSpecial(c1))
            result = -1

        if (CardType.IsSpecial(c2))
            result = 1

        return result
    }

    static SortWithDominatedLast(c1: CardType, c2: CardType) {
        var result = 10 * ((c2.CardColor + 1) * (!c2.IsDominated ? 10 : 1) - (c1.CardColor + 1) * (!c1.IsDominated ? 10 : 1)) + (c1.CardLevel - c2.CardLevel)

        if (CardType.IsSpecial(c1))
            result = 1

        if (CardType.IsSpecial(c2))
            result = 1

        return result
    }
}

export enum CardColor {
    Blue = 0,
    Orange = 1,
    Red = 2,
    Purple = 3,
    None = 4
}

export enum CardLevel {
    _6 = 0,
    _7 = 1,
    _8 = 2,
    _9 = 3,
    _10 = 4,
    Valet = 5,
    Queen = 6,
    King = 7,
    Ace = 8,
    J = 9
}

