import {Injectable, NgZone} from 'angular2/core'
import {Http} from 'angular2/http'
import {TranslateService}   from 'ng2-translate'
import {ConfigProvider} from './ConfigProvider'
import {UsersProvider} from './UsersProvider'
import {PlatformProvider} from 'Jok/Platforms'

declare var $, ga, window: any;


@Injectable()
export class CurrentUserProvider extends EventEmitter {

    public static AuthentificationChangeEvent = 'AuthentificationChangeEvent'
    public static LanguageChangeEvent = 'LanguageChangeEvent'

    defaultLang = 'en'

    langMap = {
        1: 'ka',
        2: 'en',
        3: 'ru'
    }

    UserId: number

    RatingStars: number

    AllLevels: PlayerLevel[]

    Data: any

    GamesHistory: any[] = []
    GamesHistoryInProgress: boolean
    GamesHistoryLastGetItemsCount: number

    isBuyAllowed() {
        return this.platform.isStoreReady && this.Data
    }

    isAdmin() {
        return this.UserId == 32
    }

    private onUserLoaded: () => void


    constructor(
        private config: ConfigProvider,
        private users: UsersProvider,
        private translate: TranslateService,
        private platform: PlatformProvider,
        private zone: NgZone,
        private http: Http) {
        super()

        this.setFirstLoadLanguage()
        this.setPlayerLevels()

        this.platform.on(PlatformProvider.REQUEST_SAVE_TOKEN_EVENT, sid => {
            this.refreshUserData(sid)
            document.location.reload()
        })
    }

    onReady(cb: (user: any) => void) {
        if (this.Data) {
            cb(this.Data)
            return
        }

        this.onUserLoaded = () => cb(this.Data)
    }

    useLang(lang: string, skipPosting = false) {


        this.translate.use(lang)
        this.config.activeLang = lang
        this.config.save()

        $('html').attr('lang', lang)

        if (skipPosting) return

        var url = this.config.apiUrl + '/User/UpdateLanguage/' + lang + '?sid=' + this.config.sid
        this.http.post(url, null).subscribe()

        this.emit(CurrentUserProvider.LanguageChangeEvent, lang, this.getLanguageId(lang))
    }

    getLanguageId(lang: string) {
        if (this.langMap[2] == lang)
            return 2

        if (this.langMap[3] == lang)
            return 3

        return 1
    }

    refreshUserData(sid?: string, cb?: (user?) => void) {
        var isSidChanged = false

        if (sid) {
            isSidChanged = (this.config.sid != sid)
            this.config.sid = sid
            this.config.save()

            this.platform.saveToken(sid)
        }

        this.users.getCurrent(user => this.zone.run(() => {
            this.Data = user.IsSuccess ? user : null

            if (this.Data) {
                this.UserId = this.Data.UserID
                this.RatingStars = (this.Data.Game && this.Data.Game.RatingStars) ? this.Data.Game.RatingStars : 0

                try {
                    ga('set', 'userId', this.UserId) // Google Analytics: Set the user ID using signed-in user_id.
                }
                catch (err) { }

                this.useLang(this.langMap[user.LanguageID] || this.defaultLang, !sid)


                if (isSidChanged) {
                    try {
                        this.emit(CurrentUserProvider.AuthentificationChangeEvent)
                    }
                    catch (err) {
                        console.warn('Catch', 'AuthentificationChangeEvent', err)
                    }
                }
            }

            cb && cb(this.Data)
        }))
    }

    logout() {
        this.config.sid = ''
        this.config.save()

        this.platform.saveToken('')


        if (!this.config.isMobile()) {
            var url = 'https://' + 'jok.io/portal/setsid?sid=' + '&returnUrl=' + location.toString()

            document.location.assign(url)
        }

        else {
            document.location.reload()
        }
    }

    getPlayerLevel(rating?: number) {
        if (!rating && this.RatingStars === undefined)
            return null

        if (!rating && rating != 0)
            rating = this.RatingStars

        return Math.floor(rating / 4)
    }

    getPlayerLevelName(rating?: number) {
        var level = this.getPlayerLevel(rating)

        if (level == undefined)
            level = this.getPlayerLevel()

        var overLevel = 0

        if (level < 0) level = 0
        if (level > 5) {
            overLevel = level - 5
            level = 5
        }

        return 'playerLevels.Level' + (level + 1)
    }

    getLevelStars(level: number) {
        return level * 4
    }

    getOverLevel(rating) {
        if (!rating) return 0

        var maxLevelStars = this.getLevelStars(5)
        var overLevels = Math.floor((rating - maxLevelStars) / 4)
        if (overLevels <= 0) return 0

        return overLevels + 1
    }

    refreshGamesHistory(skip?: number, take?: number) {

        if (!skip)
            this.GamesHistory = []

        this.GamesHistoryInProgress = true

        this.http.get(this.config.apiUrl + '/Joker/GamesLog' + '?skip=' + skip + '&take=' + take + '&sid=' + this.config.sid).subscribe(x => {
            this.GamesHistoryInProgress = false
            var res = x.json()

            if (!res || !res.IsSuccess) return

            this.GamesHistoryLastGetItemsCount = res.Data.length
            this.GamesHistory = this.GamesHistory.concat(res.Data)
        })
    }


    private setFirstLoadLanguage() {

        this.translate.setDefaultLang(this.defaultLang)

        var userLang = (navigator.language ? navigator.language.split('-')[0] : '') || document.documentElement.lang // use navigator lang if available
        userLang = /(ka|en|ru)/gi.test(userLang) ? userLang : this.defaultLang

        userLang = this.defaultLang

        if (!this.config.activeLang)
            this.config.activeLang = userLang

        this.useLang(this.config.activeLang, true)

        this.refreshUserData(null, () => this.onUserLoaded && this.onUserLoaded())
    }

    private setPlayerLevels() {
        var levels: PlayerLevel[] = []

        for (var i = 0; i < 6; i++) {
            levels.push({
                Stars: i * 4,
                Name: 'playerLevels.Level' + (i + 1)
            })
        }

        this.AllLevels = levels
    }
}

export interface PlayerLevel {
    Stars: number
    Name: string
}