import {Injectable} from 'angular2/core'
import {makeTypeError} from 'angular2/src/facade/exceptions';
import {Http, Request, Response, RequestOptionsArgs, RequestMethod, ConnectionBackend, RequestOptions} from 'angular2/http'
import {Observable} from 'rxjs/Rx'


@Injectable()
export class HttpProvider extends Http {

    onRequestStart: Observable<any> = new Observable().share()
    onRequestSuccess: Observable<Response> = new Observable().share()
    onRequestError: Observable<any> = new Observable().share()
    onRequestComplete: Observable<any> = new Observable().share()


    constructor(protected _backend: ConnectionBackend, protected _defaultOptions: RequestOptions) {
        super(_backend, _defaultOptions)
    }


    get(url: string, options?: RequestOptionsArgs): Observable<Response> {

        console.log(['Request Start'], url)

        var response = super.get(url, options).share()
        response.subscribe(
            () => console.log(['Request Success'], url),
            () => console.log(['Request Fail'], url),
            () => console.log(['Request Complete'], url)
        )

        return response
    }
}
