
export interface GameTable {
    ID: string
    Channel: string
    OnlinePlayersCount: number
    PlayersCount: number
    Status: TableStatuses

    Players: GamePlayer[]
    ActivePlayer: GamePlayer
    LastWinnerUserID: number
}


export class GamePlayer {
    public UserID: number
    public IPAddress: string
    public TakenCount: number
    public Score: number
    public IsOnline: boolean
    public IsAway: boolean

    public IsListening: boolean
    public MusicChannel: string
    public IsFriendRequestSent: boolean


    public LevelName: string
    public Want: number
    public Took: number
    public RelationStatusID: number
    public IsBlocked: boolean
    public IsFrendRequestSent: boolean
    public IsVIP: boolean
    public Nick: string
    public AvatarUri: string
    public IsReady: boolean
    public BirthDate: Date
    public GenderID: number
    public Position: number
    public GameLeaveCount: number
    public GamerRaiting: number
    public Place: number
    public AddedPoints: number
    public AddedRating: number

    static CreateMock(userId, nick, avatar, isOnline = true): GamePlayer {
        var item = new GamePlayer()
        item.Nick = nick
        item.UserID = userId
        item.AvatarUri = avatar
        item.Want = 2
        item.Took = Math.round(Math.random() * 3)
        item.IsOnline = isOnline
        return item
    }
}

