import {Injectable, NgZone} from 'angular2/core'
import {Router} from 'angular2/router'
import {PlatformProvider} from './Common/platform.provider'


@Injectable()
export class BrowserPlatformProvider extends PlatformProvider {

    cache = {}


    constructor(private router: Router) {
        super()
    }


    playAudio(name: string, ext: string = 'mp3') {

        var audio = this.cache[name + ext] || new Audio('/audios/' + name + '.' + ext)
        this.cache[name + ext] = audio

        try {
            audio.pause()
            audio.currentTime = 0
            audio.play()
        }
        catch (err) { }
    }

    initStore(hideLoader?: boolean) { }

    buyItem(productId: string) { }

    initGameCenter() { }

    submitScore(score: number) { }

    checkInternetConnection() { }

    showLoading(title?: string) { }

    hideLoading() { }

    saveToken(token: string) { }

    deviceRegistration() { }

    login(returnUrl?: string) {
        this.router.navigate(['/Login', { returnUrl: returnUrl }])
    }

    viewProfile(userid: number) {
        this.emit(PlatformProvider.VIEW_PROFILE_EVENT, userid)
    }
}
