import {Injectable, NgZone} from 'angular2/core'
import {Router} from 'angular2/router'
import {PlatformProvider} from './Common/platform.provider'

declare var Platform, Mt: any


@Injectable()
export class AndroidPlatformProvider extends PlatformProvider {

    constructor(private router: Router) {
        super()

        this.registerCallbackHandlers()

        setTimeout(this.registerCallbackHandlers.bind(this), 1000)
        setTimeout(this.registerCallbackHandlers.bind(this), 3000)
    }


    registerCallbackHandlers() {
        try {
            if (Platform.StoreInitializationSuccess) return

            Platform.StoreInitializationSuccess = (products: Product[]) => {
                this.isStoreReady = true
                this.emit('StoreInitializationSuccess', products)
            }
            Platform.StoreInitializationFailed = (error) => this.emit('StoreInitializationFailed', error)
            Platform.GlobalPurchaseError = (error) => this.emit('GlobalPurchaseError', error)
            Platform.PurchaseSuccess = (productId, result) => this.emit('PurchaseSuccess', productId, result)
            Platform.PurchaseFailed = (productId, error) => this.emit('PurchaseFailed', productId, error)
            Platform.DeviceRegistrationResult = (isSuccess, sid) => this.emit('DeviceRegistrationResult', isSuccess, sid)
        }
        catch (err) {
        }
    }


    playAudio(name: string, ext: string = 'mp3') {
        try {
            Platform.PlayAudio(name, ext)
        }
        catch (err) {
            console.error('AndroidPlatformProvider.playAudio', err)
        }
    }

    initStore(hideLoader: boolean = true) {
        try {
            Platform.InitStore(hideLoader)
        }
        catch (err) {
            console.error('AndroidPlatformProvider.initStore', err)
        }
    }

    buyItem(productId: string) {
        try {
            Platform.BuyItem(productId)
        }
        catch (err) {
            console.error('AndroidPlatformProvider.buyItem', err)
        }
    }

    initGameCenter() {
        try {
            Platform.InitGameCenter()
        }
        catch (err) {
            console.error('AndroidPlatformProvider.initGameCenter', err)
        }
    }

    submitScore(score: number) {
        try {
            Platform.SubmitScore(score)
        }
        catch (err) {
            console.error('AndroidPlatformProvider.submitScore', err)
        }
    }

    checkInternetConnection() {
        try {
            Platform.CheckInternetConnection()
        }
        catch (err) {
            console.error('AndroidPlatformProvider.checkInternetConnection', err)
        }
    }

    showLoading(title?: string) {
        try {
            Platform.ShowLoading(title)
        }
        catch (err) {
            console.error('AndroidPlatformProvider.showLoading', err)
        }
    }

    hideLoading() {
        try {
            Platform.HideLoading()
        }
        catch (err) {
            console.error('AndroidPlatformProvider.hideLoading', err)
        }
    }

    saveToken(token: string) {
        try {
            Platform.SaveToken(token)
        }
        catch (err) {
            console.error('AndroidPlatformProvider.saveToken', err)
        }
    }

    deviceRegistration() {
        try {
            Platform.DeviceRegistration()
        }
        catch (err) {
            alert(err);
            console.error('IOSPlatformProvider.deviceRegistration', err)
        }
    }

    login(returnUrl?: string) {
        this.router.navigate(['/Login', { returnUrl: returnUrl }])
    }

    viewProfile(userid: number) {
        this.emit(PlatformProvider.VIEW_PROFILE_EVENT, userid)
    }
}


interface Product {
    ID: string,
    PriceFormatted: string,
    Price: number
}