import {Component}  from 'angular2/core'
import {RouteConfig, ROUTER_DIRECTIVES, RouteParams, Router} from 'angular2/router'

import {UIPlay} from './play'
import {UIMock} from './mock'


@Component({
    selector: 'joker',
    template: '<router-outlet></router-outlet>',
    directives: [ROUTER_DIRECTIVES]
})
@RouteConfig([
    { path: '/', name: 'Public', component: UIPlay, useAsDefault: true },
    { path: '/mode/:mode', name: 'PublicWithMode', component: UIPlay },
    { path: '/mock', name: 'Mock', component: UIMock },
    { path: '/:channel/:mode', name: 'PrivateWithMode', component: UIPlay },
    { path: '/:channel', name: 'Private', component: UIPlay },
])
export class Route {

}
