import {Injectable, EventEmitter} from 'angular2/core'


@Injectable()
export class ProfileProvider {

    activateEvent = new EventEmitter<any>()
    chatBubbleEvent = new EventEmitter<any>()
    activateCompletedEvent = new EventEmitter()
    stopActivateEvent = new EventEmitter()

    private activateCompletedCallback = null


    constructor() {
        this.activateCompletedEvent.subscribe(() => {
            this.activateCompletedCallback && this.activateCompletedCallback()
        })
    }


    activate(userid: number, duration: number, cb?: () => void) {
        this.activateEvent.emit({
            UserId: userid,
            Duration: duration
        })
        this.activateCompletedCallback = cb
    }

    stopActivate() {
        this.stopActivateEvent.emit(null)
    }

    chatBubble(msg: string, userid: number) {
        this.chatBubbleEvent.emit({
            Message: msg,
            UserId: userid
        })
    }
}