import {Injectable, NgZone} from 'angular2/core'
import {Router} from 'angular2/router'
import {PlatformProvider} from './Common/platform.provider'

declare var Platform, Mt, window: any


@Injectable()
export class WindowsPhonePlatformProvider extends PlatformProvider {

    cache = {}

    constructor(private router: Router) {
        super()

        this.registerCallbackHandlers()

        setTimeout(this.registerCallbackHandlers.bind(this), 1000)
        setTimeout(this.registerCallbackHandlers.bind(this), 3000)
    }


    registerCallbackHandlers() {
        try {
            if (Platform.StoreInitializationSuccess) return

            Platform.StoreInitializationSuccess = (products: Product[]) => {
                this.isStoreReady = true
                this.emit('StoreInitializationSuccess', products)
            }
            Platform.StoreInitializationFailed = (error) => this.emit('StoreInitializationFailed', error)
            Platform.GlobalPurchaseError = (error) => this.emit('GlobalPurchaseError', error)
            Platform.PurchaseSuccess = (productId, result) => this.emit('PurchaseSuccess', productId, result)
            Platform.PurchaseFailed = (productId, error) => this.emit('PurchaseFailed', productId, error)
            Platform.DeviceRegistrationResult = (isSuccess, sid) => this.emit('DeviceRegistrationResult', isSuccess, sid)
        }
        catch (err) {
        }
    }


    playAudio(name: string, ext: string = 'mp3') {
        try {

            //var obj = {
            //    Action: 'PlayAudio',
            //    Args: [name, ext]
            //}

            //window.external.notify(JSON.stringify(obj))


            var audio = this.cache[name + ext] || new Audio('/audios/' + name + '.' + ext)
            this.cache[name + ext] = audio

            try {
                audio.pause()
                audio.currentTime = 0
                audio.play()
            }
            catch (err) { }

        }
        catch (err) {
            console.error('WindowsPhonePlatformProvider.playAudio', err)
        }
    }

    initStore(hideLoader: boolean = true) {
        try {
            Mt.App.fireEvent('AppStoreInit', {
                HideLoader: hideLoader
            });
        }
        catch (err) {
            console.error('IOSPlatformProvider.initStore', err)
        }
    }

    buyItem(productId: string) {
        try {
            Mt.App.fireEvent('BuyItem', {
                ID: productId,
                ShowLoader: true
            });
        }
        catch (err) {
            console.error('IOSPlatformProvider.buyItem', err)
        }
    }

    initGameCenter() {
        try {
            Mt.App.fireEvent('GameCenterInit', {});
        }
        catch (err) {
            console.error('IOSPlatformProvider.initGameCenter', err)
        }
    }

    submitScore(score: number) {
        try {
            Mt.App.fireEvent('SubmitScore', {
                score: score
            });

        }
        catch (err) {
            console.error('IOSPlatformProvider.submitScore', err)
        }
    }

    checkInternetConnection() {
        try {
            Mt.App.fireEvent('CheckConnection', {});
        }
        catch (err) {
            console.error('IOSPlatformProvider.checkInternetConnection', err)
        }
    }

    showLoading(title?: string) {
        try {
            Mt.App.fireEvent('Loading', {
                visible: true,
                title: title
            });

        }
        catch (err) {
            console.error('IOSPlatformProvider.showLoading', err)
        }
    }

    hideLoading() {
        try {
            Mt.App.fireEvent('Loading', {
                visible: false
            });
        }
        catch (err) {
            console.error('IOSPlatformProvider.hideLoading', err)
        }
    }

    saveToken(token: string) {
        try {
            Mt.App.fireEvent('Token', {
                Token: token
            });
        }
        catch (err) {
            console.error('IOSPlatformProvider.saveToken', err)
        }
    }

    deviceRegistration() {
        try {
            Mt.App.fireEvent('DeviceRegistration', {})
        }
        catch (err) {
            alert(err);
            console.error('IOSPlatformProvider.deviceRegistration', err)
        }
    }

    login(returnUrl?: string) {
        this.router.navigate(['/Login', { returnUrl: returnUrl }])
    }

    viewProfile(userid: number) {
        this.emit(PlatformProvider.VIEW_PROFILE_EVENT, userid)
    }
}


interface Product {
    ID: string,
    PriceFormatted: string,
    Price: number
}