///<reference path="../node_modules/angular2/typings/browser.d.ts"/>
declare var window: any

// Angular
import {enableProdMode, provide, PLATFORM_PIPES, ExceptionHandler} from 'angular2/core'
import {Http, HTTP_PROVIDERS} from 'angular2/http'
import {bootstrap, Title}  from 'angular2/platform/browser'
import {ROUTER_PROVIDERS} from 'angular2/router';
import {TranslateService, TranslateLoader, TranslateStaticLoader, TranslatePipe} from 'ng2-translate'

// Modules
import {EmotionsPipe, ChatProvider} from 'Jok/Chat'
import {PlatformProvider, Platform} from 'Jok/Platforms'
import {DownCardsProvider} from 'Jok/Cards'
import {EscapeWantZero} from 'Jok/Joker'
import {MusicProvider} from 'Jok/Music'

// Common
import {AppComponent}  from './app'
import {ConfigProvider, UsersProvider, PortalProvider, CurrentUserProvider} from './Common/Providers/All';
import {JokExceptionHandler} from './Common/JokExceptionHandler'
import {Welcome} from './Common/Welcome'


enableProdMode()

bootstrap(AppComponent,
    [
        HTTP_PROVIDERS,
        ROUTER_PROVIDERS,
        UsersProvider,
        PortalProvider,
        ConfigProvider,
        ChatProvider,
        TranslateService,
        CurrentUserProvider,
        MusicProvider,
        Title,
        provide(PlatformProvider, { useClass: Platform.GetPlatform() }),
        provide(TranslateLoader, { useFactory: (http: Http) => new TranslateStaticLoader(http, 'lang', '.json?version=' + window.ClientVersion), deps: [Http] }),
        provide(PLATFORM_PIPES, { useValue: [TranslatePipe, EmotionsPipe], multi: true }),
        provide(ExceptionHandler, { useClass: JokExceptionHandler })
    ]
)

Welcome.Show()